import React, { useState, useEffect } from 'react'
import { rhuService } from '../../../../services/rhuService'

const OrderOfPayments = () => {
  const [orders, setOrders] = useState([])
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')
  const [success, setSuccess] = useState('')
  const [showOrderModal, setShowOrderModal] = useState(false)
  const [viewingOrder, setViewingOrder] = useState(null)
  
  // Form states for new order
  const [patientName, setPatientName] = useState('')
  const [birthdate, setBirthdate] = useState('')
  const [address, setAddress] = useState('')
  const [serviceSearch, setServiceSearch] = useState('')
  const [searchResults, setSearchResults] = useState([])
  const [selectedServices, setSelectedServices] = useState([])
  const [searchingServices, setSearchingServices] = useState(false)

  useEffect(() => {
    loadOrders()
  }, [])

  const loadOrders = async () => {
    setLoading(true)
    const result = await rhuService.getAllOrders()
    if (result.success) {
      setOrders(result.data)
    } else {
      setError(result.error)
      setTimeout(() => setError(''), 3000)
    }
    setLoading(false)
  }

  const calculateAge = (birthdate) => {
    if (!birthdate) return ''
    const today = new Date()
    const birth = new Date(birthdate)
    let age = today.getFullYear() - birth.getFullYear()
    const monthDiff = today.getMonth() - birth.getMonth()
    if (monthDiff < 0 || (monthDiff === 0 && today.getDate() < birth.getDate())) {
      age--
    }
    return age
  }

  const handleServiceSearch = async (term) => {
    setServiceSearch(term)
    if (term.trim().length < 2) {
      setSearchResults([])
      return
    }

    setSearchingServices(true)
    const result = await rhuService.searchServices(term)
    if (result.success) {
      setSearchResults(result.data)
    }
    setSearchingServices(false)
  }

  const addServiceToOrder = (service) => {
    const existing = selectedServices.find(s => s.service_id === service.id)
    if (existing) {
      setError('Service already added')
      setTimeout(() => setError(''), 3000)
      return
    }

    setSelectedServices([...selectedServices, {
      service_id: service.id,
      service_name: service.name,
      unit_price: parseFloat(service.price),
      quantity: 1,
      category: service.subcategory?.category?.name || '',
      subcategory: service.subcategory?.name || ''
    }])
    setServiceSearch('')
    setSearchResults([])
  }

  const removeService = (serviceId) => {
    setSelectedServices(selectedServices.filter(s => s.service_id !== serviceId))
  }

  const updateQuantity = (serviceId, quantity) => {
    if (quantity < 1) return
    setSelectedServices(selectedServices.map(s => 
      s.service_id === serviceId ? { ...s, quantity: parseInt(quantity) } : s
    ))
  }

  const calculateTotal = () => {
    return selectedServices.reduce((sum, item) => {
      return sum + (item.unit_price * item.quantity)
    }, 0)
  }

  const handleCreateOrder = async () => {
    if (!patientName.trim() || !birthdate || !address.trim()) {
      setError('Please fill in all patient information')
      setTimeout(() => setError(''), 3000)
      return
    }

    if (selectedServices.length === 0) {
      setError('Please add at least one service')
      setTimeout(() => setError(''), 3000)
      return
    }

    setLoading(true)

    // Create or get patient
    const patientResult = await rhuService.addPatient({
      name: patientName,
      birthdate: birthdate,
      address: address
    })

    if (!patientResult.success) {
      setError(patientResult.error)
      setTimeout(() => setError(''), 3000)
      setLoading(false)
      return
    }

    // Create order
    const orderResult = await rhuService.createOrder({
      patient_id: patientResult.data.id,
      items: selectedServices
    })

    if (orderResult.success) {
      setSuccess('Order created successfully!')
      resetForm()
      setShowOrderModal(false)
      loadOrders()
      setTimeout(() => setSuccess(''), 3000)
    } else {
      setError(orderResult.error)
      setTimeout(() => setError(''), 3000)
    }

    setLoading(false)
  }

  const resetForm = () => {
    setPatientName('')
    setBirthdate('')
    setAddress('')
    setServiceSearch('')
    setSearchResults([])
    setSelectedServices([])
  }

  const handleViewOrder = async (order) => {
    setLoading(true)
    const result = await rhuService.getOrderWithItems(order.id)
    if (result.success) {
      setViewingOrder(result.data)
    } else {
      setError(result.error)
      setTimeout(() => setError(''), 3000)
    }
    setLoading(false)
  }

  const handleDeleteOrder = async (orderId) => {
    if (!window.confirm('Are you sure you want to delete this order?')) return

    setLoading(true)
    const result = await rhuService.deleteOrder(orderId)
    if (result.success) {
      setSuccess('Order deleted successfully')
      loadOrders()
      setTimeout(() => setSuccess(''), 3000)
    } else {
      setError(result.error)
      setTimeout(() => setError(''), 3000)
    }
    setLoading(false)
  }

  const getStatusColor = (status) => {
    switch (status) {
      case 'paid': return 'is-success'
      case 'pending': return 'is-warning'
      case 'cancelled': return 'is-danger'
      default: return 'is-light'
    }
  }

  return (
    <>
      {/* CSS Animations */}
      <style>{`
        @keyframes fadeInUp {
          from { opacity: 0; transform: translateY(20px); }
          to { opacity: 1; transform: translateY(0); }
        }
        @keyframes fadeIn {
          from { opacity: 0; }
          to { opacity: 1; }
        }
        @keyframes slideInDown {
          from { opacity: 0; transform: translateY(-10px); }
          to { opacity: 1; transform: translateY(0); }
        }
        @keyframes pulse {
          0%, 100% { transform: scale(1); opacity: 1; }
          50% { transform: scale(1.05); opacity: 0.8; }
        }
        
        /* Custom scrollbar styling for modal */
        .modal-card-body::-webkit-scrollbar {
          width: 8px;
        }
        .modal-card-body::-webkit-scrollbar-track {
          background: #f1f1f1;
          border-radius: 10px;
        }
        .modal-card-body::-webkit-scrollbar-thumb {
          background: #dc2626;
          border-radius: 10px;
        }
        .modal-card-body::-webkit-scrollbar-thumb:hover {
          background: #b91c1c;
        }
        
        /* Scrollbar for search dropdown */
        .search-results-dropdown::-webkit-scrollbar {
          width: 6px;
        }
        .search-results-dropdown::-webkit-scrollbar-track {
          background: #f9fafb;
        }
        .search-results-dropdown::-webkit-scrollbar-thumb {
          background: #dc2626;
          border-radius: 3px;
        }
        
        @media (max-width: 768px) {
          .level { flex-direction: column; align-items: flex-start !important; }
          .level-right, .level-left { width: 100%; }
          .buttons { width: 100%; justify-content: flex-start !important; }
          
          /* Modal adjustments for mobile */
          .modal-card {
            max-height: 95vh !important;
            margin: 0.5rem !important;
            width: calc(100% - 1rem) !important;
          }
          
          .modal-card-body {
            max-height: calc(95vh - 140px) !important;
            padding: 1rem !important;
            overflow-y: auto !important;
            overflow-x: hidden !important;
            -webkit-overflow-scrolling: touch !important;
          }
          
          /* Ensure search dropdown is visible on mobile */
          .field {
            position: relative;
          }
        }
      `}</style>

    <div className="section" style={{ padding: '1.5rem 1rem' }}>
      <div className="container" style={{ maxWidth: '1400px' }}>
        {/* Header with gradient background */}
        <div style={{ 
          background: 'linear-gradient(135deg, #ef4444 0%, #dc2626 100%)', 
          borderRadius: '16px',
          padding: '2rem',
          marginBottom: '2rem',
          boxShadow: '0 10px 30px rgba(239, 68, 68, 0.2)'
        }}>
          <div className="level mb-0" style={{ flexWrap: 'wrap', alignItems: 'center' }}>
            <div className="level-left" style={{ marginBottom: '0.5rem' }}>
              <div className="level-item">
                <div style={{ maxWidth: '100%' }}>
                  <h1 className="title is-3" style={{ 
                    color: 'white', 
                    marginBottom: '0.75rem',
                    lineHeight: '1.2',
                    display: 'flex',
                    alignItems: 'center',
                    flexWrap: 'wrap'
                  }}>
                    <i className="fas fa-file-invoice-dollar" style={{ marginRight: '0.75rem' }}></i>
                    <span>Order of Payments</span>
                  </h1>
                  <p className="subtitle is-6" style={{ 
                    color: 'rgba(255, 255, 255, 0.9)', 
                    marginBottom: 0,
                    marginTop: '0.5rem',
                    lineHeight: '1.5'
                  }}>
                    Generate and manage patient service orders
                  </p>
                </div>
              </div>
            </div>
            <div className="level-right" style={{ marginTop: '0.5rem' }}>
              <div className="level-item">
                <button 
                  className="button is-light is-medium"
                  onClick={() => {
                    resetForm()
                    setShowOrderModal(true)
                  }}
                  style={{ 
                    fontWeight: '600',
                    borderRadius: '12px',
                    boxShadow: '0 4px 12px rgba(0, 0, 0, 0.15)',
                    transition: 'all 0.3s ease'
                  }}
                  onMouseEnter={(e) => e.currentTarget.style.transform = 'translateY(-2px)'}
                  onMouseLeave={(e) => e.currentTarget.style.transform = 'translateY(0)'}
                >
                  <span className="icon">
                    <i className="fas fa-plus-circle"></i>
                  </span>
                  <span>New Order</span>
                </button>
              </div>
            </div>
          </div>
        </div>

        {/* Success Message */}
        {success && (
          <div className="notification is-success is-light" style={{
            borderRadius: '12px',
            border: '2px solid #48c774',
            animation: 'slideInDown 0.3s ease',
            marginBottom: '1.5rem'
          }}>
            <button className="delete" onClick={() => setSuccess('')}></button>
            <i className="fas fa-check-circle" style={{ marginRight: '0.5rem' }}></i>
            {success}
          </div>
        )}

        {/* Error Message */}
        {error && (
          <div className="notification is-danger is-light" style={{
            borderRadius: '12px',
            border: '2px solid #f14668',
            animation: 'slideInDown 0.3s ease',
            marginBottom: '1.5rem'
          }}>
            <button className="delete" onClick={() => setError('')}></button>
            <i className="fas fa-exclamation-circle" style={{ marginRight: '0.5rem' }}></i>
            {error}
          </div>
        )}

        {/* Loading */}
        {loading && (
          <div className="notification is-info is-light" style={{
            borderRadius: '12px',
            textAlign: 'center',
            marginBottom: '1.5rem'
          }}>
            <span className="icon" style={{ fontSize: '1.5rem' }}>
              <i className="fas fa-spinner fa-spin"></i>
            </span>
            <span style={{ marginLeft: '0.5rem', fontSize: '1.1rem' }}>Loading...</span>
          </div>
        )}

        {/* Orders List */}
        {orders.length === 0 && !loading ? (
          <div className="box" style={{ 
            textAlign: 'center', 
            padding: '4rem 2rem',
            borderRadius: '16px',
            border: '2px dashed #e5e7eb',
            background: 'linear-gradient(135deg, #fef2f2 0%, #fee2e2 100%)'
          }}>
            <div style={{ 
              fontSize: '4rem', 
              color: '#ef4444', 
              marginBottom: '1.5rem',
              animation: 'pulse 2s infinite'
            }}>
              <i className="fas fa-file-invoice"></i>
            </div>
            <h3 className="title is-4" style={{ color: '#363636' }}>No Orders Yet</h3>
            <p className="subtitle is-6" style={{ color: '#6b7280', maxWidth: '400px', margin: '0 auto 1.5rem' }}>
              Create your first order of payment for a patient
            </p>
            <button 
              className="button is-danger is-medium"
              onClick={() => {
                resetForm()
                setShowOrderModal(true)
              }}
              style={{ borderRadius: '12px' }}
            >
              <span className="icon">
                <i className="fas fa-plus-circle"></i>
              </span>
              <span>Create First Order</span>
            </button>
          </div>
        ) : (
          <div className="box" style={{
            borderRadius: '16px',
            padding: '1.5rem',
            boxShadow: '0 4px 12px rgba(0, 0, 0, 0.08)'
          }}>
            <div style={{ overflowX: 'auto' }}>
              <table className="table is-fullwidth is-hoverable" style={{ 
                background: 'white',
                borderRadius: '10px'
              }}>
                <thead>
                  <tr style={{ background: '#f8f9fa' }}>
                    <th style={{ borderRadius: '10px 0 0 0' }}>Order #</th>
                    <th>Patient Name</th>
                    <th>Age</th>
                    <th>Total Amount</th>
                    <th>Status</th>
                    <th>Date</th>
                    <th style={{ borderRadius: '0 10px 0 0', width: '150px' }}>Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {orders.map((order, index) => (
                    <tr 
                      key={order.id}
                      style={{
                        animation: `fadeIn 0.3s ease ${index * 0.05}s both`,
                        transition: 'all 0.3s ease'
                      }}
                      onMouseEnter={(e) => e.currentTarget.style.background = '#f8f9fa'}
                      onMouseLeave={(e) => e.currentTarget.style.background = ''}
                    >
                      <td className="has-text-weight-semibold" style={{ color: '#ef4444' }}>
                        {order.order_number}
                      </td>
                      <td className="has-text-weight-semibold">{order.patient?.name || 'N/A'}</td>
                      <td>{calculateAge(order.patient?.birthdate)} years</td>
                      <td>
                        <span className="tag is-warning is-medium" style={{ 
                          borderRadius: '8px',
                          fontWeight: '700',
                          fontSize: '1rem',
                          padding: '0.5rem 1rem'
                        }}>
                          {parseFloat(order.total_amount).toFixed(2)}
                        </span>
                      </td>
                      <td>
                        <span className={`tag ${getStatusColor(order.status)}`} style={{ borderRadius: '8px' }}>
                          {order.status}
                        </span>
                      </td>
                      <td>{new Date(order.created_at).toLocaleDateString()}</td>
                      <td>
                        <div className="buttons are-small">
                          <button 
                            className="button is-info is-light is-small"
                            onClick={() => handleViewOrder(order)}
                            style={{ borderRadius: '8px' }}
                          >
                            <span className="icon">
                              <i className="fas fa-eye"></i>
                            </span>
                          </button>
                          <button 
                            className="button is-danger is-light is-small"
                            onClick={() => handleDeleteOrder(order.id)}
                            style={{ borderRadius: '8px' }}
                          >
                            <span className="icon">
                              <i className="fas fa-trash"></i>
                            </span>
                          </button>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        )}

        {/* New Order Modal */}
        {showOrderModal && (
          <div className="modal is-active">
            <div className="modal-background" onClick={() => setShowOrderModal(false)} style={{ background: 'rgba(0, 0, 0, 0.6)' }}></div>
            <div className="modal-card" style={{ 
              borderRadius: '16px', 
              overflow: 'hidden',
              animation: 'fadeInUp 0.3s ease',
              maxWidth: '800px',
              width: '90%'
            }}>
              <header className="modal-card-head" style={{ 
                background: 'linear-gradient(135deg, #ef4444 0%, #dc2626 100%)',
                borderBottom: 'none'
              }}>
                <p className="modal-card-title" style={{ color: 'white', fontWeight: '600' }}>
                  <i className="fas fa-file-invoice-dollar" style={{ marginRight: '0.5rem' }}></i>
                  Create New Order of Payment
                </p>
                <button className="delete" onClick={() => setShowOrderModal(false)} style={{ background: 'rgba(255, 255, 255, 0.3)' }}></button>
              </header>
              <section className="modal-card-body" style={{ 
                padding: '2rem', 
                maxHeight: '70vh', 
                overflowY: 'auto',
                overflowX: 'hidden',
                WebkitOverflowScrolling: 'touch',
                position: 'relative'
              }}>
                {/* Patient Information */}
                <div style={{ marginBottom: '2rem', padding: '1.5rem', background: '#fef2f2', borderRadius: '12px', border: '1px solid #fecaca' }}>
                  <h3 className="title is-5" style={{ marginBottom: '1rem', color: '#991b1b' }}>
                    <i className="fas fa-user" style={{ marginRight: '0.5rem' }}></i>
                    Patient Information
                  </h3>
                  
                  <div className="field">
                    <label className="label" style={{ fontWeight: '600', color: '#2c3e50' }}>Patient Name *</label>
                    <div className="control">
                      <input 
                        className="input"
                        type="text"
                        placeholder="e.g., Juan Dela Cruz"
                        value={patientName}
                        onChange={(e) => setPatientName(e.target.value)}
                        style={{ borderRadius: '10px', border: '2px solid #e5e7eb' }}
                      />
                    </div>
                  </div>

                  <div className="columns">
                    <div className="column">
                      <div className="field">
                        <label className="label" style={{ fontWeight: '600', color: '#2c3e50' }}>Birthdate *</label>
                        <div className="control">
                          <input 
                            className="input"
                            type="date"
                            value={birthdate}
                            onChange={(e) => setBirthdate(e.target.value)}
                            style={{ borderRadius: '10px', border: '2px solid #e5e7eb' }}
                          />
                        </div>
                      </div>
                    </div>
                    <div className="column is-narrow" style={{ minWidth: '130px' }}>
                      <div className="field">
                        <label className="label" style={{ fontWeight: '600', color: '#2c3e50' }}>Age</label>
                        <div className="control">
                          <input 
                            className="input"
                            type="text"
                            value={calculateAge(birthdate) ? `${calculateAge(birthdate)} years` : ''}
                            readOnly
                            style={{ borderRadius: '10px', border: '2px solid #e5e7eb', background: '#f9fafb' }}
                          />
                        </div>
                      </div>
                    </div>
                    <div className="column">
                      <div className="field">
                        <label className="label" style={{ fontWeight: '600', color: '#2c3e50' }}>Barangay *</label>
                        <div className="control">
                          <div className="select is-fullwidth">
                            <select 
                              value={address}
                              onChange={(e) => setAddress(e.target.value)}
                              style={{ borderRadius: '10px', border: '2px solid #e5e7eb' }}
                            >
                              <option value="">Select Barangay</option>
                              <option value="Bakhawan">Bakhawan</option>
                              <option value="Calabasahan">Calabasahan</option>
                              <option value="Dalajican">Dalajican</option>
                              <option value="Masadya">Masadya</option>
                              <option value="Masudsud">Masudsud</option>
                              <option value="Poblacion">Poblacion</option>
                              <option value="San Pedro">San Pedro</option>
                              <option value="San Vicente">San Vicente</option>
                              <option value="Sampong">Sampong</option>
                            </select>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

                {/* Services Section */}
                <div style={{ marginBottom: '2rem' }}>
                  <h3 className="title is-5" style={{ marginBottom: '1rem', color: '#991b1b' }}>
                    <i className="fas fa-clipboard-list" style={{ marginRight: '0.5rem' }}></i>
                    Services
                  </h3>

                  {/* Service Search */}
                  <div className="field" style={{ position: 'relative', overflow: 'visible' }}>
                    <label className="label" style={{ fontWeight: '600', color: '#2c3e50' }}>Search Services</label>
                    <div className="control has-icons-left" style={{ position: 'relative', overflow: 'visible' }}>
                      <input 
                        className="input"
                        type="text"
                        placeholder="Search by service, category, or subcategory..."
                        value={serviceSearch}
                        onChange={(e) => handleServiceSearch(e.target.value)}
                        style={{ borderRadius: '10px', border: '2px solid #e5e7eb' }}
                      />
                      <span className="icon is-left">
                        <i className={searchingServices ? "fas fa-spinner fa-spin" : "fas fa-search"}></i>
                      </span>
                    </div>
                    <p className="help" style={{ marginTop: '0.25rem', fontSize: '0.8rem', color: '#6b7280' }}>
                      <i className="fas fa-info-circle" style={{ marginRight: '0.25rem' }}></i>
                      You can search by service name, category (e.g., Medical, Dental), or subcategory
                    </p>

                    {/* Search Results Dropdown */}
                    {searchResults.length > 0 && serviceSearch.trim().length >= 2 && (
                      <div className="search-results-dropdown" style={{
                        position: 'absolute',
                        zIndex: 9999,
                        background: 'white',
                        border: '2px solid #e5e7eb',
                        borderRadius: '10px',
                        marginTop: '0.5rem',
                        maxHeight: '200px',
                        overflowY: 'auto',
                        WebkitOverflowScrolling: 'touch',
                        boxShadow: '0 4px 12px rgba(0, 0, 0, 0.1)',
                        width: '100%',
                        left: 0,
                        right: 0
                      }}>
                        {searchResults.map(service => (
                          <div
                            key={service.id}
                            onClick={() => addServiceToOrder(service)}
                            style={{
                              padding: '0.75rem 1rem',
                              cursor: 'pointer',
                              borderBottom: '1px solid #f3f4f6',
                              transition: 'background 0.2s'
                            }}
                            onMouseEnter={(e) => e.currentTarget.style.background = '#f9fafb'}
                            onMouseLeave={(e) => e.currentTarget.style.background = 'white'}
                          >
                            <div style={{ fontWeight: '600', color: '#2c3e50' }}>{service.name}</div>
                            <div style={{ fontSize: '0.85rem', color: '#6b7280' }}>
                              {service.subcategory?.category?.name}  {service.subcategory?.name}  {parseFloat(service.price).toFixed(2)}
                            </div>
                          </div>
                        ))}
                      </div>
                    )}

                    {/* No Results Message */}
                    {searchResults.length === 0 && serviceSearch.trim().length >= 2 && !searchingServices && (
                      <div style={{
                        position: 'absolute',
                        zIndex: 9999,
                        background: 'white',
                        border: '2px solid #e5e7eb',
                        borderRadius: '10px',
                        marginTop: '0.5rem',
                        padding: '1rem',
                        boxShadow: '0 4px 12px rgba(0, 0, 0, 0.1)',
                        width: '100%',
                        left: 0,
                        right: 0,
                        textAlign: 'center'
                      }}>
                        <i className="fas fa-search" style={{ fontSize: '2rem', color: '#d1d5db', marginBottom: '0.5rem' }}></i>
                        <p style={{ color: '#6b7280', fontSize: '0.9rem', marginBottom: '0.25rem' }}>
                          No services found for "<strong>{serviceSearch}</strong>"
                        </p>
                        <p style={{ color: '#9ca3af', fontSize: '0.8rem', margin: 0 }}>
                          Try searching by category (Medical, Dental, Lab) or subcategory
                        </p>
                      </div>
                    )}
                  </div>

                  {/* Selected Services */}
                  {selectedServices.length > 0 ? (
                    <div style={{ marginTop: '1.5rem' }}>
                      <table className="table is-fullwidth" style={{ background: 'white', borderRadius: '10px' }}>
                        <thead>
                          <tr style={{ background: '#f8f9fa' }}>
                            <th>Service</th>
                            <th style={{ width: '120px' }}>Quantity</th>
                            <th style={{ width: '120px' }}>Price</th>
                            <th style={{ width: '120px' }}>Subtotal</th>
                            <th style={{ width: '60px' }}></th>
                          </tr>
                        </thead>
                        <tbody>
                          {selectedServices.map(item => (
                            <tr key={item.service_id}>
                              <td>
                                <div style={{ fontWeight: '600' }}>{item.service_name}</div>
                                <div style={{ fontSize: '0.85rem', color: '#6b7280' }}>
                                  {item.category}  {item.subcategory}
                                </div>
                              </td>
                              <td>
                                <input 
                                  className="input is-small"
                                  type="number"
                                  min="1"
                                  value={item.quantity}
                                  onChange={(e) => updateQuantity(item.service_id, e.target.value)}
                                  style={{ borderRadius: '8px', width: '80px' }}
                                />
                              </td>
                              <td>{item.unit_price.toFixed(2)}</td>
                              <td style={{ fontWeight: '700' }}>{(item.unit_price * item.quantity).toFixed(2)}</td>
                              <td>
                                <button 
                                  className="button is-danger is-small"
                                  onClick={() => removeService(item.service_id)}
                                  style={{ borderRadius: '8px' }}
                                >
                                  <span className="icon">
                                    <i className="fas fa-times"></i>
                                  </span>
                                </button>
                              </td>
                            </tr>
                          ))}
                        </tbody>
                        <tfoot>
                          <tr style={{ background: '#fef2f2' }}>
                            <td colSpan="3" style={{ textAlign: 'right', fontWeight: '700', fontSize: '1.1rem' }}>
                              TOTAL AMOUNT:
                            </td>
                            <td colSpan="2" style={{ fontWeight: '700', fontSize: '1.2rem', color: '#ef4444' }}>
                              {calculateTotal().toFixed(2)}
                            </td>
                          </tr>
                        </tfoot>
                      </table>
                    </div>
                  ) : (
                    <div style={{ 
                      textAlign: 'center', 
                      padding: '2rem', 
                      background: '#f9fafb', 
                      borderRadius: '10px',
                      border: '2px dashed #e5e7eb',
                      marginTop: '1rem'
                    }}>
                      <i className="fas fa-inbox" style={{ fontSize: '2rem', color: '#9ca3af', marginBottom: '0.5rem' }}></i>
                      <p style={{ color: '#6b7280', margin: 0 }}>No services added yet. Search and add services above.</p>
                    </div>
                  )}
                </div>
              </section>
              <footer className="modal-card-foot" style={{ 
                borderTop: '1px solid #e5e7eb',
                background: '#f9fafb',
                justifyContent: 'flex-end',
                gap: '0.75rem'
              }}>
                <button className="button" onClick={() => setShowOrderModal(false)} style={{ borderRadius: '10px' }}>Cancel</button>
                <button 
                  className="button is-danger" 
                  onClick={handleCreateOrder}
                  disabled={loading || selectedServices.length === 0}
                  style={{ borderRadius: '10px', fontWeight: '600' }}
                >
                  <span className="icon">
                    <i className="fas fa-check-circle"></i>
                  </span>
                  <span>Create Order</span>
                </button>
              </footer>
            </div>
          </div>
        )}

        {/* View Order Modal */}
        {viewingOrder && (
          <div className="modal is-active">
            <div className="modal-background" onClick={() => setViewingOrder(null)} style={{ background: 'rgba(0, 0, 0, 0.6)' }}></div>
            <div className="modal-card" style={{ 
              borderRadius: '16px', 
              overflow: 'hidden',
              animation: 'fadeInUp 0.3s ease',
              maxWidth: '700px'
            }}>
              <header className="modal-card-head" style={{ 
                background: 'linear-gradient(135deg, #3b82f6 0%, #2563eb 100%)',
                borderBottom: 'none'
              }}>
                <p className="modal-card-title" style={{ color: 'white', fontWeight: '600' }}>
                  <i className="fas fa-file-invoice" style={{ marginRight: '0.5rem' }}></i>
                  Order Details - {viewingOrder.order_number}
                </p>
                <button className="delete" onClick={() => setViewingOrder(null)} style={{ background: 'rgba(255, 255, 255, 0.3)' }}></button>
              </header>
              <section className="modal-card-body" style={{ padding: '2rem' }}>
                {/* Patient Info */}
                <div style={{ marginBottom: '2rem', padding: '1.5rem', background: '#eff6ff', borderRadius: '12px' }}>
                  <h3 className="title is-6" style={{ marginBottom: '1rem', color: '#1e40af' }}>
                    <i className="fas fa-user" style={{ marginRight: '0.5rem' }}></i>
                    Patient Information
                  </h3>
                  <p><strong>Name:</strong> {viewingOrder.patient?.name}</p>
                  <p><strong>Age:</strong> {calculateAge(viewingOrder.patient?.birthdate)} years</p>
                  <p><strong>Address:</strong> {viewingOrder.patient?.address}</p>
                </div>

                {/* Order Items */}
                <div style={{ marginBottom: '1.5rem' }}>
                  <h3 className="title is-6" style={{ marginBottom: '1rem', color: '#1e40af' }}>
                    <i className="fas fa-list" style={{ marginRight: '0.5rem' }}></i>
                    Services
                  </h3>
                  <table className="table is-fullwidth">
                    <thead>
                      <tr style={{ background: '#f8f9fa' }}>
                        <th>Service</th>
                        <th style={{ width: '80px' }}>Qty</th>
                        <th style={{ width: '120px' }}>Price</th>
                        <th style={{ width: '120px' }}>Subtotal</th>
                      </tr>
                    </thead>
                    <tbody>
                      {viewingOrder.items?.map(item => (
                        <tr key={item.id}>
                          <td>{item.service_name}</td>
                          <td>{item.quantity}</td>
                          <td>{parseFloat(item.unit_price).toFixed(2)}</td>
                          <td style={{ fontWeight: '700' }}>{parseFloat(item.subtotal).toFixed(2)}</td>
                        </tr>
                      ))}
                    </tbody>
                    <tfoot>
                      <tr style={{ background: '#eff6ff' }}>
                        <td colSpan="3" style={{ textAlign: 'right', fontWeight: '700', fontSize: '1.1rem' }}>
                          TOTAL:
                        </td>
                        <td style={{ fontWeight: '700', fontSize: '1.2rem', color: '#3b82f6' }}>
                          {parseFloat(viewingOrder.total_amount).toFixed(2)}
                        </td>
                      </tr>
                    </tfoot>
                  </table>
                </div>

                {/* Payment Information for Paid Orders */}
                {viewingOrder.status === 'paid' && (
                  <div style={{ marginBottom: '1.5rem', padding: '1.5rem', background: '#f0fdf4', borderRadius: '12px', border: '1px solid #bbf7d0' }}>
                    <h3 className="title is-6" style={{ marginBottom: '1rem', color: '#166534' }}>
                      <i className="fas fa-check-circle" style={{ marginRight: '0.5rem' }}></i>
                      Payment Information
                    </h3>
                    <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '1rem' }}>
                      <div>
                        <p style={{ fontSize: '0.9rem', color: '#6b7280', marginBottom: '0.25rem' }}>OR Number:</p>
                        <p style={{ fontSize: '1.1rem', fontWeight: '700', color: '#166534' }}>
                          {viewingOrder.or_number || 'N/A'}
                        </p>
                      </div>
                      <div>
                        <p style={{ fontSize: '0.9rem', color: '#6b7280', marginBottom: '0.25rem' }}>Payment Date:</p>
                        <p style={{ fontSize: '1.1rem', fontWeight: '700', color: '#166534' }}>
                          {viewingOrder.paid_at ? new Date(viewingOrder.paid_at).toLocaleString() : 'N/A'}
                        </p>
                      </div>
                      <div style={{ gridColumn: '1 / -1' }}>
                        <p style={{ fontSize: '0.9rem', color: '#6b7280', marginBottom: '0.25rem' }}>Processed By:</p>
                        <p style={{ fontSize: '1.1rem', fontWeight: '700', color: '#166534' }}>
                          <i className="fas fa-user" style={{ marginRight: '0.5rem' }}></i>
                          {viewingOrder.processed_by || 'N/A'}
                        </p>
                      </div>
                    </div>
                  </div>
                )}

                {/* Status */}
                <div style={{ textAlign: 'center' }}>
                  <span className={`tag is-large ${getStatusColor(viewingOrder.status)}`} style={{ borderRadius: '10px' }}>
                    Status: {viewingOrder.status.toUpperCase()}
                  </span>
                </div>
              </section>
              <footer className="modal-card-foot" style={{ 
                borderTop: '1px solid #e5e7eb',
                background: '#f9fafb',
                justifyContent: 'center'
              }}>
                <button className="button is-info" onClick={() => setViewingOrder(null)} style={{ borderRadius: '10px' }}>Close</button>
              </footer>
            </div>
          </div>
        )}
      </div>
    </div>
    </>
  )
}

export default OrderOfPayments
