import React, { useState } from 'react'
import { authService } from '../../authService'

const ProfileModal = ({ user, isOpen, onClose, onUpdate }) => {
  const [isEditing, setIsEditing] = useState(false)
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')
  const [success, setSuccess] = useState('')
  const [formData, setFormData] = useState({
    firstname: user.firstname || '',
    lastname: user.lastname || '',
    email: user.email || '',
    phone: user.phone || '',
    address: user.address || ''
  })

  if (!isOpen) return null

  const handleChange = (field, value) => {
    setFormData(prev => ({ ...prev, [field]: value }))
    setError('')
  }

  const handleSave = async () => {
    setLoading(true)
    setError('')
    setSuccess('')

    try {
      const result = await authService.updateProfile(user.userid, formData)
      if (result.success) {
        setSuccess('Profile updated successfully!')
        setIsEditing(false)
        // Update parent component with new user data
        onUpdate({ ...user, ...formData })
        setTimeout(() => {
          setSuccess('')
        }, 2000)
      } else {
        setError(result.error || 'Failed to update profile')
      }
    } catch (err) {
      setError('An error occurred while updating profile')
    }
    setLoading(false)
  }

  const handleCancel = () => {
    setFormData({
      firstname: user.firstname || '',
      lastname: user.lastname || '',
      email: user.email || '',
      phone: user.phone || '',
      address: user.address || ''
    })
    setIsEditing(false)
    setError('')
    setSuccess('')
  }

  const formatDate = (dateString) => {
    if (!dateString) return 'N/A'
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    })
  }

  return (
    <div className="modal is-active">
      <div className="modal-background" onClick={onClose}></div>
      <div className="modal-card" style={{ width: '600px', maxWidth: '90vw' }}>
        <header className="modal-card-head">
          <p className="modal-card-title">
            <i className="fas fa-user-circle"></i> My Profile
          </p>
          <button className="delete" aria-label="close" onClick={onClose}></button>
        </header>
        <section className="modal-card-body">
          {/* Error/Success Messages */}
          {error && (
            <div className="notification is-danger is-light">
              <button className="delete" onClick={() => setError('')}></button>
              {error}
            </div>
          )}
          {success && (
            <div className="notification is-success is-light">
              <button className="delete" onClick={() => setSuccess('')}></button>
              {success}
            </div>
          )}

          {/* User Avatar and Info */}
          <div style={{ textAlign: 'center', marginBottom: '2rem' }}>
            <div style={{
              width: '100px',
              height: '100px',
              borderRadius: '50%',
              background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
              display: 'inline-flex',
              alignItems: 'center',
              justifyContent: 'center',
              fontSize: '2.5rem',
              color: 'white',
              fontWeight: 'bold',
              marginBottom: '1rem'
            }}>
              {user.firstname?.charAt(0)}{user.lastname?.charAt(0)}
            </div>
            <h3 className="title is-4" style={{ marginBottom: '0.25rem' }}>
              {user.firstname} {user.lastname}
            </h3>
            <p className="subtitle is-6" style={{ color: '#7a7a7a' }}>
              <i className="fas fa-shield-alt"></i> {user.role}
            </p>
          </div>

          {/* Profile Information */}
          <div className="box" style={{ background: '#f9fafb', padding: '1.5rem' }}>
            <div className="columns is-multiline">
              <div className="column is-6">
                <label className="label" style={{ fontSize: '0.875rem', color: '#6b7280' }}>
                  First Name
                </label>
                {isEditing ? (
                  <input
                    className="input"
                    type="text"
                    value={formData.firstname}
                    onChange={(e) => handleChange('firstname', e.target.value)}
                  />
                ) : (
                  <p style={{ fontSize: '1rem', fontWeight: '500', marginTop: '0.5rem' }}>
                    {user.firstname}
                  </p>
                )}
              </div>

              <div className="column is-6">
                <label className="label" style={{ fontSize: '0.875rem', color: '#6b7280' }}>
                  Last Name
                </label>
                {isEditing ? (
                  <input
                    className="input"
                    type="text"
                    value={formData.lastname}
                    onChange={(e) => handleChange('lastname', e.target.value)}
                  />
                ) : (
                  <p style={{ fontSize: '1rem', fontWeight: '500', marginTop: '0.5rem' }}>
                    {user.lastname}
                  </p>
                )}
              </div>

              <div className="column is-12">
                <label className="label" style={{ fontSize: '0.875rem', color: '#6b7280' }}>
                  <i className="fas fa-envelope"></i> Email Address
                </label>
                {isEditing ? (
                  <input
                    className="input"
                    type="email"
                    value={formData.email}
                    onChange={(e) => handleChange('email', e.target.value)}
                  />
                ) : (
                  <p style={{ fontSize: '1rem', fontWeight: '500', marginTop: '0.5rem' }}>
                    {user.email}
                  </p>
                )}
              </div>

              <div className="column is-6">
                <label className="label" style={{ fontSize: '0.875rem', color: '#6b7280' }}>
                  <i className="fas fa-phone"></i> Phone Number
                </label>
                {isEditing ? (
                  <input
                    className="input"
                    type="text"
                    value={formData.phone}
                    onChange={(e) => handleChange('phone', e.target.value)}
                    placeholder="Enter phone number"
                  />
                ) : (
                  <p style={{ fontSize: '1rem', fontWeight: '500', marginTop: '0.5rem' }}>
                    {user.phone || 'Not provided'}
                  </p>
                )}
              </div>

              <div className="column is-6">
                <label className="label" style={{ fontSize: '0.875rem', color: '#6b7280' }}>
                  <i className="fas fa-user-tag"></i> User ID
                </label>
                <p style={{ fontSize: '1rem', fontWeight: '500', marginTop: '0.5rem', color: '#2563eb' }}>
                  {user.userid}
                </p>
              </div>

              <div className="column is-12">
                <label className="label" style={{ fontSize: '0.875rem', color: '#6b7280' }}>
                  <i className="fas fa-map-marker-alt"></i> Address
                </label>
                {isEditing ? (
                  <textarea
                    className="textarea"
                    value={formData.address}
                    onChange={(e) => handleChange('address', e.target.value)}
                    placeholder="Enter address"
                    rows="2"
                  />
                ) : (
                  <p style={{ fontSize: '1rem', fontWeight: '500', marginTop: '0.5rem' }}>
                    {user.address || 'Not provided'}
                  </p>
                )}
              </div>

              <div className="column is-6">
                <label className="label" style={{ fontSize: '0.875rem', color: '#6b7280' }}>
                  <i className="fas fa-calendar-plus"></i> Account Created
                </label>
                <p style={{ fontSize: '0.875rem', color: '#6b7280', marginTop: '0.5rem' }}>
                  {formatDate(user.datecreated)}
                </p>
              </div>

              <div className="column is-6">
                <label className="label" style={{ fontSize: '0.875rem', color: '#6b7280' }}>
                  <i className="fas fa-clock"></i> Last Login
                </label>
                <p style={{ fontSize: '0.875rem', color: '#6b7280', marginTop: '0.5rem' }}>
                  {formatDate(user.lastlogin)}
                </p>
              </div>

              <div className="column is-12">
                <label className="label" style={{ fontSize: '0.875rem', color: '#6b7280' }}>
                  <i className="fas fa-toggle-on"></i> Account Status
                </label>
                <span className={`tag ${user.status === 'active' ? 'is-success' : 'is-danger'}`}>
                  {user.status}
                </span>
              </div>
            </div>
          </div>
        </section>
        <footer className="modal-card-foot" style={{ justifyContent: 'flex-end', gap: '0.5rem' }}>
          {isEditing ? (
            <>
              <button
                className="button"
                onClick={handleCancel}
                disabled={loading}
              >
                Cancel
              </button>
              <button
                className={`button is-primary ${loading ? 'is-loading' : ''}`}
                onClick={handleSave}
                disabled={loading}
              >
                <span className="icon">
                  <i className="fas fa-save"></i>
                </span>
                <span>Save Changes</span>
              </button>
            </>
          ) : (
            <>
              <button className="button" onClick={onClose}>
                Close
              </button>
              <button className="button is-info" onClick={() => setIsEditing(true)}>
                <span className="icon">
                  <i className="fas fa-edit"></i>
                </span>
                <span>Edit Profile</span>
              </button>
            </>
          )}
        </footer>
      </div>
    </div>
  )
}

export default ProfileModal
