import React, { useState, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import { authService } from '../authService'

const SystemsPortal = ({ onLogout }) => {
  const [user, setUser] = useState(null)
  const [isDropdownOpen, setIsDropdownOpen] = useState(false)
  const navigate = useNavigate()

  const systems = [
    {
      id: 'water-billing',
      name: 'Water Billing System',
      description: 'Manage water consumption billing and payments',
      icon: 'fas fa-tint',
      color: '#3b82f6',
      gradient: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
      path: '/portal/water-billing/dashboard',
      available: true
    },
    {
      id: 'property-tax',
      name: 'Real Property Tax System',
      description: 'Real property tax assessment and collection',
      icon: 'fas fa-building',
      color: '#10b981',
      gradient: 'linear-gradient(135deg, #0ea5e9 0%, #06b6d4 100%)',
      path: '/portal/property-tax',
      available: false
    },
    {
      id: 'business-permit',
      name: 'Business Permit & Licensing',
      description: 'Business registration and permit processing',
      icon: 'fas fa-briefcase',
      color: '#f59e0b',
      gradient: 'linear-gradient(135deg, #f59e0b 0%, #d97706 100%)',
      path: '/portal/business-permit',
      available: false
    },
    {
      id: 'treasury',
      name: 'Treasury Management',
      description: 'Revenue collection and financial management',
      icon: 'fas fa-coins',
      color: '#8b5cf6',
      gradient: 'linear-gradient(135deg, #8b5cf6 0%, #7c3aed 100%)',
      path: '/portal/treasury',
      available: false
    },
    {
      id: 'rural-health',
      name: 'Rural Health Unit',
      description: 'Health services, patient records, and medical management',
      icon: 'fas fa-heartbeat',
      color: '#ef4444',
      gradient: 'linear-gradient(135deg, #ef4444 0%, #dc2626 100%)',
      path: '/portal/rural-health/dashboard',
      available: true
    }
  ]

  useEffect(() => {
    const currentUser = authService.getUserSession()
    if (!currentUser) {
      navigate('/login')
    } else {
      setUser(currentUser)
    }
  }, [navigate])

  useEffect(() => {
    const handleClickOutside = (event) => {
      const dropdown = document.querySelector('.user-dropdown')
      if (dropdown && !dropdown.contains(event.target)) {
        setIsDropdownOpen(false)
      }
    }

    if (isDropdownOpen) {
      document.addEventListener('mousedown', handleClickOutside)
      return () => document.removeEventListener('mousedown', handleClickOutside)
    }
  }, [isDropdownOpen])

  const handleLogout = () => {
    authService.logout()
    if (onLogout) {
      onLogout()
    }
    navigate('/login', { replace: true })
  }

  const handleSystemClick = (system) => {
    if (system.available) {
      // Store user session for the system
      sessionStorage.setItem('currentSystem', system.id)
      navigate(system.path)
    }
  }

  if (!user) {
    return (
      <div className="container">
        <div className="section">
          <div className="has-text-centered">
            <button className="button is-loading is-large">Loading</button>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="systems-portal">
      {/* Header */}
      <header className="portal-header">
        <div className="portal-header-content">
          <div className="portal-branding">
            <div className="brand-icon">
              <i className="fas fa-landmark"></i>
            </div>
            <div className="brand-text">
              <h1 className="brand-title">Municipal Treasurer's Office</h1>
              <p className="brand-subtitle">Online Systems Portal</p>
            </div>
          </div>
          
          <div className="portal-user-section">
            <div className="user-info">
              <div className="user-name">{user.firstname} {user.lastname}</div>
              <div className="user-role">{user.role}</div>
            </div>
            
            <div className={`user-dropdown ${isDropdownOpen ? 'is-active' : ''}`}>
              <button 
                className="user-button"
                onClick={() => setIsDropdownOpen(!isDropdownOpen)}
              >
                <div className="user-avatar">
                  {user.firstname.charAt(0)}{user.lastname.charAt(0)}
                </div>
                <i className="fas fa-chevron-down" style={{ fontSize: '0.75rem', color: '#9ca3af' }}></i>
              </button>
              
              <div className="dropdown-menu">
                <button className="dropdown-item" onClick={handleLogout}>
                  <i className="fas fa-sign-out-alt"></i>
                  <span>Logout</span>
                </button>
              </div>
            </div>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="portal-main">
        <div className="portal-container">
          {/* Welcome Section */}
          <div className="portal-welcome">
            <h2 className="welcome-title">Welcome back, {user.firstname}!</h2>
            <p className="welcome-subtitle">Select a system to access</p>
          </div>

          {/* Systems Grid */}
          <div className="systems-grid">
            {systems.map((system) => (
              <div
                key={system.id}
                className={`system-card ${system.available ? 'available' : 'unavailable'}`}
                onClick={() => handleSystemClick(system)}
                style={{
                  cursor: system.available ? 'pointer' : 'not-allowed'
                }}
              >
                <div className="system-card-header" style={{ background: system.gradient }}>
                  <div className="system-icon">
                    <i className={system.icon}></i>
                  </div>
                  {!system.available && (
                    <div className="coming-soon-badge">
                      <i className="fas fa-clock"></i>
                      <span>Coming Soon</span>
                    </div>
                  )}
                </div>
                
                <div className="system-card-body">
                  <h3 className="system-name">{system.name}</h3>
                  <p className="system-description">{system.description}</p>
                  
                  {system.available && (
                    <button className="system-access-btn">
                      <span>Access System</span>
                      <i className="fas fa-arrow-right"></i>
                    </button>
                  )}
                </div>
              </div>
            ))}
          </div>

          {/* Footer Info */}
          <div className="portal-footer">
            <p className="footer-text">
              <i className="fas fa-info-circle"></i>
              Contact IT Support for system access issues or technical assistance
            </p>
          </div>
        </div>
      </main>
    </div>
  )
}

export default SystemsPortal
