import React, { useState, useEffect } from 'react'
import { rhuService } from '../../../../services/rhuService'

const Services = () => {
  const [categories, setCategories] = useState([])
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')
  const [success, setSuccess] = useState('')
  const [showCategoryModal, setShowCategoryModal] = useState(false)
  const [showSubcategoryModal, setShowSubcategoryModal] = useState(false)
  const [showServiceModal, setShowServiceModal] = useState(false)
  const [selectedCategory, setSelectedCategory] = useState(null)
  const [selectedSubcategory, setSelectedSubcategory] = useState(null)
  const [expandedCategories, setExpandedCategories] = useState([])
  const [expandedSubcategories, setExpandedSubcategories] = useState([])
  
  // Edit mode states
  const [editMode, setEditMode] = useState(null) // 'category', 'subcategory', 'service', or null
  const [editingItem, setEditingItem] = useState(null)
  
  // Form states
  const [categoryName, setCategoryName] = useState('')
  const [subcategoryName, setSubcategoryName] = useState('')
  const [serviceName, setServiceName] = useState('')
  const [servicePrice, setServicePrice] = useState('')
  const [serviceDescription, setServiceDescription] = useState('')

  // Load from Supabase on mount
  useEffect(() => {
    loadAllData()
  }, [])

  const loadAllData = async () => {
    setLoading(true)
    const result = await rhuService.getAllServicesHierarchy()
    if (result.success) {
      setCategories(result.data)
    } else {
      setError(result.error)
    }
    setLoading(false)
  }

  const openAddCategoryModal = () => {
    setEditMode(null)
    setEditingItem(null)
    setCategoryName('')
    setShowCategoryModal(true)
  }

  const openEditCategoryModal = (category) => {
    setEditMode('category')
    setEditingItem(category)
    setCategoryName(category.name)
    setShowCategoryModal(true)
  }

  const handleSaveCategory = async () => {
    if (!categoryName.trim()) return

    setLoading(true)
    let result
    
    if (editMode === 'category' && editingItem) {
      result = await rhuService.updateCategory(editingItem.id, { name: categoryName })
      if (result.success) {
        setSuccess('Category updated successfully')
      }
    } else {
      result = await rhuService.addCategory({ name: categoryName })
      if (result.success) {
        setSuccess('Category added successfully')
      }
    }
    
    if (result.success) {
      setCategoryName('')
      setShowCategoryModal(false)
      setEditMode(null)
      setEditingItem(null)
      loadAllData()
      setTimeout(() => setSuccess(''), 3000)
    } else {
      setError(result.error)
      setTimeout(() => setError(''), 3000)
    }
    setLoading(false)
  }

  const openAddSubcategoryModal = (category) => {
    setEditMode(null)
    setEditingItem(null)
    setSelectedCategory(category)
    setSubcategoryName('')
    setShowSubcategoryModal(true)
  }

  const openEditSubcategoryModal = (category, subcategory) => {
    setEditMode('subcategory')
    setEditingItem(subcategory)
    setSelectedCategory(category)
    setSubcategoryName(subcategory.name)
    setShowSubcategoryModal(true)
  }

  const handleSaveSubcategory = async () => {
    if (!subcategoryName.trim() || !selectedCategory) return

    setLoading(true)
    let result
    
    if (editMode === 'subcategory' && editingItem) {
      result = await rhuService.updateSubcategory(editingItem.id, { name: subcategoryName })
      if (result.success) {
        setSuccess('Subcategory updated successfully')
      }
    } else {
      result = await rhuService.addSubcategory({
        category_id: selectedCategory.id,
        name: subcategoryName
      })
      if (result.success) {
        setSuccess('Subcategory added successfully')
      }
    }
    
    if (result.success) {
      setSubcategoryName('')
      setShowSubcategoryModal(false)
      setSelectedCategory(null)
      setEditMode(null)
      setEditingItem(null)
      loadAllData()
      setTimeout(() => setSuccess(''), 3000)
    } else {
      setError(result.error)
      setTimeout(() => setError(''), 3000)
    }
    setLoading(false)
  }

  const openAddServiceModal = (category, subcategory) => {
    setEditMode(null)
    setEditingItem(null)
    setSelectedCategory(category)
    setSelectedSubcategory(subcategory)
    setServiceName('')
    setServicePrice('')
    setServiceDescription('')
    setShowServiceModal(true)
  }

  const openEditServiceModal = (category, subcategory, service) => {
    setEditMode('service')
    setEditingItem(service)
    setSelectedCategory(category)
    setSelectedSubcategory(subcategory)
    setServiceName(service.name)
    setServicePrice(service.price.toString())
    setServiceDescription(service.description || '')
    setShowServiceModal(true)
  }

  const handleSaveService = async () => {
    if (!serviceName.trim() || !servicePrice || !selectedCategory || !selectedSubcategory) return

    setLoading(true)
    let result
    
    if (editMode === 'service' && editingItem) {
      result = await rhuService.updateService(editingItem.id, {
        name: serviceName,
        price: parseFloat(servicePrice),
        description: serviceDescription
      })
      if (result.success) {
        setSuccess('Service updated successfully')
      }
    } else {
      result = await rhuService.addService({
        subcategory_id: selectedSubcategory.id,
        name: serviceName,
        price: parseFloat(servicePrice),
        description: serviceDescription
      })
      if (result.success) {
        setSuccess('Service added successfully')
      }
    }
    
    if (result.success) {
      setServiceName('')
      setServicePrice('')
      setServiceDescription('')
      setShowServiceModal(false)
      setSelectedCategory(null)
      setSelectedSubcategory(null)
      setEditMode(null)
      setEditingItem(null)
      loadAllData()
      setTimeout(() => setSuccess(''), 3000)
    } else {
      setError(result.error)
      setTimeout(() => setError(''), 3000)
    }
    setLoading(false)
  }

  const toggleCategory = (categoryId) => {
    if (expandedCategories.includes(categoryId)) {
      setExpandedCategories(expandedCategories.filter(id => id !== categoryId))
    } else {
      setExpandedCategories([...expandedCategories, categoryId])
    }
  }

  const toggleSubcategory = (subcategoryId) => {
    if (expandedSubcategories.includes(subcategoryId)) {
      setExpandedSubcategories(expandedSubcategories.filter(id => id !== subcategoryId))
    } else {
      setExpandedSubcategories([...expandedSubcategories, subcategoryId])
    }
  }

  const deleteCategory = async (categoryId) => {
    if (!window.confirm('Are you sure you want to delete this category and all its contents?')) return

    setLoading(true)
    const result = await rhuService.deleteCategory(categoryId)
    
    if (result.success) {
      setSuccess('Category deleted successfully')
      loadAllData()
      setTimeout(() => setSuccess(''), 3000)
    } else {
      setError(result.error)
      setTimeout(() => setError(''), 3000)
    }
    setLoading(false)
  }

  const deleteSubcategory = async (categoryId, subcategoryId) => {
    if (!window.confirm('Are you sure you want to delete this subcategory and all its services?')) return

    setLoading(true)
    const result = await rhuService.deleteSubcategory(subcategoryId)
    
    if (result.success) {
      setSuccess('Subcategory deleted successfully')
      loadAllData()
      setTimeout(() => setSuccess(''), 3000)
    } else {
      setError(result.error)
      setTimeout(() => setError(''), 3000)
    }
    setLoading(false)
  }

  const deleteService = async (categoryId, subcategoryId, serviceId) => {
    if (!window.confirm('Are you sure you want to delete this service?')) return

    setLoading(true)
    const result = await rhuService.deleteService(serviceId)
    
    if (result.success) {
      setSuccess('Service deleted successfully')
      loadAllData()
      setTimeout(() => setSuccess(''), 3000)
    } else {
      setError(result.error)
      setTimeout(() => setError(''), 3000)
    }
    setLoading(false)
  }

  return (
    <>
      {/* CSS Animations */}
      <style>{`
        @keyframes fadeInUp {
          from {
            opacity: 0;
            transform: translateY(20px);
          }
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }
        
        @keyframes fadeInLeft {
          from {
            opacity: 0;
            transform: translateX(-20px);
          }
          to {
            opacity: 1;
            transform: translateX(0);
          }
        }
        
        @keyframes fadeIn {
          from { opacity: 0; }
          to { opacity: 1; }
        }
        
        @keyframes slideInDown {
          from {
            opacity: 0;
            transform: translateY(-10px);
          }
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }
        
        @keyframes pulse {
          0%, 100% {
            transform: scale(1);
            opacity: 1;
          }
          50% {
            transform: scale(1.05);
            opacity: 0.8;
          }
        }
        
        @media (max-width: 768px) {
          .level {
            flex-direction: column;
            align-items: flex-start !important;
          }
          
          .level-right, .level-left {
            width: 100%;
          }
          
          .buttons {
            width: 100%;
            justify-content: flex-start !important;
          }
        }
      `}</style>
    <div className="section" style={{ padding: '1.5rem 1rem' }}>
      <div className="container" style={{ maxWidth: '1400px' }}>
        {/* Header with gradient background */}
        <div style={{ 
          background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)', 
          borderRadius: '16px',
          padding: '2rem',
          marginBottom: '2rem',
          boxShadow: '0 10px 30px rgba(102, 126, 234, 0.2)'
        }}>
          <div className="level mb-0" style={{ flexWrap: 'wrap', alignItems: 'center' }}>
            <div className="level-left" style={{ marginBottom: '0.5rem' }}>
              <div className="level-item">
                <div style={{ maxWidth: '100%' }}>
                  <h1 className="title is-3" style={{ 
                    color: 'white', 
                    marginBottom: '0.75rem',
                    lineHeight: '1.2',
                    display: 'flex',
                    alignItems: 'center',
                    flexWrap: 'wrap'
                  }}>
                    <i className="fas fa-clipboard-list" style={{ marginRight: '0.75rem' }}></i>
                    <span>RHU Services Management</span>
                  </h1>
                  <p className="subtitle is-6" style={{ 
                    color: 'rgba(255, 255, 255, 0.9)', 
                    marginBottom: 0,
                    marginTop: '0.5rem',
                    lineHeight: '1.5'
                  }}>
                    Manage service categories, subcategories, and pricing with ease
                  </p>
                </div>
              </div>
            </div>
            <div className="level-right" style={{ marginTop: '0.5rem' }}>
              <div className="level-item">
                <button 
                  className="button is-light is-medium"
                  onClick={openAddCategoryModal}
                  style={{ 
                    fontWeight: '600',
                    borderRadius: '12px',
                    boxShadow: '0 4px 12px rgba(0, 0, 0, 0.15)',
                    transition: 'all 0.3s ease'
                  }}
                  onMouseEnter={(e) => e.currentTarget.style.transform = 'translateY(-2px)'}
                  onMouseLeave={(e) => e.currentTarget.style.transform = 'translateY(0)'}
                >
                  <span className="icon">
                    <i className="fas fa-plus-circle"></i>
                  </span>
                  <span>Add Category</span>
                </button>
              </div>
            </div>
          </div>
        </div>

        {/* Success Message with animation */}
        {success && (
          <div className="notification is-success is-light" style={{
            borderRadius: '12px',
            border: '2px solid #48c774',
            animation: 'slideInDown 0.3s ease',
            marginBottom: '1.5rem'
          }}>
            <button className="delete" onClick={() => setSuccess('')}></button>
            <i className="fas fa-check-circle" style={{ marginRight: '0.5rem' }}></i>
            {success}
          </div>
        )}

        {/* Error Message with animation */}
        {error && (
          <div className="notification is-danger is-light" style={{
            borderRadius: '12px',
            border: '2px solid #f14668',
            animation: 'slideInDown 0.3s ease',
            marginBottom: '1.5rem'
          }}>
            <button className="delete" onClick={() => setError('')}></button>
            <i className="fas fa-exclamation-circle" style={{ marginRight: '0.5rem' }}></i>
            {error}
          </div>
        )}

        {/* Loading Indicator with better styling */}
        {loading && (
          <div className="notification is-info is-light" style={{
            borderRadius: '12px',
            textAlign: 'center',
            marginBottom: '1.5rem'
          }}>
            <span className="icon" style={{ fontSize: '1.5rem' }}>
              <i className="fas fa-spinner fa-spin"></i>
            </span>
            <span style={{ marginLeft: '0.5rem', fontSize: '1.1rem' }}>Loading...</span>
          </div>
        )}

        {/* Categories List */}
        {categories.length === 0 && !loading ? (
          <div className="box" style={{ 
            textAlign: 'center', 
            padding: '4rem 2rem',
            borderRadius: '16px',
            border: '2px dashed #e5e7eb',
            background: 'linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%)'
          }}>
            <div style={{ 
              fontSize: '4rem', 
              color: '#667eea', 
              marginBottom: '1.5rem',
              animation: 'pulse 2s infinite'
            }}>
              <i className="fas fa-folder-open"></i>
            </div>
            <h3 className="title is-4" style={{ color: '#363636' }}>No Categories Yet</h3>
            <p className="subtitle is-6" style={{ color: '#6b7280', maxWidth: '400px', margin: '0 auto 1.5rem' }}>
              Start by adding your first service category to organize your RHU services
            </p>
            <button 
              className="button is-primary is-medium"
              onClick={openAddCategoryModal}
              style={{ borderRadius: '12px' }}
            >
              <span className="icon">
                <i className="fas fa-plus-circle"></i>
              </span>
              <span>Create First Category</span>
            </button>
          </div>
        ) : (
          <div style={{ display: 'flex', flexDirection: 'column', gap: '1.5rem' }}>
            {categories.map((category, index) => (
              <div 
                key={category.id} 
                className="box"
                style={{ 
                  borderRadius: '16px',
                  border: '2px solid #f0f0f0',
                  padding: '1.5rem',
                  boxShadow: '0 4px 12px rgba(0, 0, 0, 0.08)',
                  transition: 'all 0.3s ease',
                  animation: `fadeInUp 0.4s ease ${index * 0.1}s both`,
                  cursor: 'pointer'
                }}
                onMouseEnter={(e) => {
                  e.currentTarget.style.boxShadow = '0 8px 24px rgba(102, 126, 234, 0.15)'
                  e.currentTarget.style.transform = 'translateY(-2px)'
                }}
                onMouseLeave={(e) => {
                  e.currentTarget.style.boxShadow = '0 4px 12px rgba(0, 0, 0, 0.08)'
                  e.currentTarget.style.transform = 'translateY(0)'
                }}
              >
                {/* Category Header */}
                <div className="level is-mobile">
                  <div className="level-left">
                    <div className="level-item">
                      <button 
                        className="button is-small"
                        onClick={() => toggleCategory(category.id)}
                        style={{ 
                          marginRight: '0.75rem',
                          borderRadius: '8px',
                          border: 'none',
                          background: expandedCategories.includes(category.id) ? '#667eea' : '#f5f5f5',
                          color: expandedCategories.includes(category.id) ? 'white' : '#4a4a4a',
                          transition: 'all 0.3s ease'
                        }}
                      >
                        <i className={`fas fa-chevron-${expandedCategories.includes(category.id) ? 'down' : 'right'}`}></i>
                      </button>
                      <span className="icon-text">
                        <span className="icon" style={{ 
                          color: '#667eea',
                          fontSize: '1.5rem',
                          marginRight: '0.5rem'
                        }}>
                          <i className="fas fa-folder"></i>
                        </span>
                        <span className="has-text-weight-bold is-size-5" style={{ color: '#2c3e50' }}>
                          {category.name}
                        </span>
                        <span className="tag is-primary is-light ml-2" style={{ 
                          borderRadius: '8px',
                          fontWeight: '600'
                        }}>
                          {category.subcategories.length} subcategories
                        </span>
                      </span>
                    </div>
                  </div>
                  <div className="level-right">
                    <div className="level-item">
                      <div className="buttons are-small" style={{ flexWrap: 'wrap' }}>
                        <button 
                          className="button is-info is-light"
                          onClick={() => openEditCategoryModal(category)}
                          style={{ 
                            borderRadius: '10px',
                            fontWeight: '500',
                            transition: 'all 0.3s ease'
                          }}
                          onMouseEnter={(e) => {
                            e.currentTarget.style.background = '#3273dc'
                            e.currentTarget.style.color = 'white'
                          }}
                          onMouseLeave={(e) => {
                            e.currentTarget.style.background = ''
                            e.currentTarget.style.color = ''
                          }}
                        >
                          <span className="icon">
                            <i className="fas fa-edit"></i>
                          </span>
                          <span>Edit</span>
                        </button>
                        <button 
                          className="button is-success is-light"
                          onClick={() => openAddSubcategoryModal(category)}
                          style={{ 
                            borderRadius: '10px',
                            fontWeight: '500',
                            transition: 'all 0.3s ease'
                          }}
                          onMouseEnter={(e) => {
                            e.currentTarget.style.background = '#48c774'
                            e.currentTarget.style.color = 'white'
                          }}
                          onMouseLeave={(e) => {
                            e.currentTarget.style.background = ''
                            e.currentTarget.style.color = ''
                          }}
                        >
                          <span className="icon">
                            <i className="fas fa-plus"></i>
                          </span>
                          <span>Add Subcategory</span>
                        </button>
                        <button 
                          className="button is-danger is-light"
                          onClick={() => deleteCategory(category.id)}
                          style={{ 
                            borderRadius: '10px',
                            transition: 'all 0.3s ease'
                          }}
                          onMouseEnter={(e) => {
                            e.currentTarget.style.background = '#f14668'
                            e.currentTarget.style.color = 'white'
                          }}
                          onMouseLeave={(e) => {
                            e.currentTarget.style.background = ''
                            e.currentTarget.style.color = ''
                          }}
                        >
                          <span className="icon">
                            <i className="fas fa-trash"></i>
                          </span>
                        </button>
                      </div>
                    </div>
                  </div>
                </div>

                {/* Subcategories */}
                {expandedCategories.includes(category.id) && (
                  <div style={{ 
                    marginLeft: '1.5rem', 
                    marginTop: '1.5rem',
                    paddingLeft: '1.5rem',
                    borderLeft: '3px solid #667eea'
                  }}>
                    {category.subcategories.length === 0 ? (
                      <div style={{ 
                        padding: '2rem',
                        textAlign: 'center',
                        background: '#f8f9fa',
                        borderRadius: '12px',
                        border: '2px dashed #dee2e6'
                      }}>
                        <i className="fas fa-folder-open" style={{ fontSize: '2rem', color: '#adb5bd', marginBottom: '0.5rem' }}></i>
                        <p style={{ fontStyle: 'italic', color: '#6c757d' }}>No subcategories yet. Add one to get started!</p>
                      </div>
                    ) : (
                      category.subcategories.map((subcategory, subIndex) => (
                        <div 
                          key={subcategory.id} 
                          className="box mb-3" 
                          style={{ 
                            background: 'linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%)',
                            borderRadius: '12px',
                            border: '1px solid #dee2e6',
                            boxShadow: '0 2px 8px rgba(0, 0, 0, 0.06)',
                            animation: `fadeInLeft 0.3s ease ${subIndex * 0.05}s both`
                          }}>
                          {/* Subcategory Header */}
                          <div className="level is-mobile">
                            <div className="level-left">
                              <div className="level-item">
                                <button 
                                  className="button is-small"
                                  onClick={() => toggleSubcategory(subcategory.id)}
                                  style={{ 
                                    marginRight: '0.5rem',
                                    borderRadius: '6px',
                                    border: 'none',
                                    background: expandedSubcategories.includes(subcategory.id) ? '#10b981' : '#e9ecef',
                                    color: expandedSubcategories.includes(subcategory.id) ? 'white' : '#495057',
                                    transition: 'all 0.3s ease'
                                  }}
                                >
                                  <i className={`fas fa-chevron-${expandedSubcategories.includes(subcategory.id) ? 'down' : 'right'}`}></i>
                                </button>
                                <span className="icon-text">
                                  <span className="icon" style={{ 
                                    color: '#10b981',
                                    fontSize: '1.2rem',
                                    marginRight: '0.5rem'
                                  }}>
                                    <i className="fas fa-layer-group"></i>
                                  </span>
                                  <span className="has-text-weight-semibold" style={{ fontSize: '1.1rem' }}>{subcategory.name}</span>
                                  <span className="tag is-success is-light ml-2" style={{ 
                                    borderRadius: '6px',
                                    fontWeight: '600'
                                  }}>
                                    {subcategory.services.length} services
                                  </span>
                                </span>
                              </div>
                            </div>
                            <div className="level-right">
                              <div className="level-item">
                                <div className="buttons are-small" style={{ flexWrap: 'wrap' }}>
                                  <button 
                                    className="button is-info is-light is-small"
                                    onClick={() => openEditSubcategoryModal(category, subcategory)}
                                    style={{ 
                                      borderRadius: '8px',
                                      fontWeight: '500',
                                      transition: 'all 0.3s ease'
                                    }}
                                    onMouseEnter={(e) => {
                                      e.currentTarget.style.background = '#3273dc'
                                      e.currentTarget.style.color = 'white'
                                    }}
                                    onMouseLeave={(e) => {
                                      e.currentTarget.style.background = ''
                                      e.currentTarget.style.color = ''
                                    }}
                                  >
                                    <span className="icon">
                                      <i className="fas fa-edit"></i>
                                    </span>
                                    <span>Edit</span>
                                  </button>
                                  <button 
                                    className="button is-success is-light is-small"
                                    onClick={() => openAddServiceModal(category, subcategory)}
                                    style={{ 
                                      borderRadius: '8px',
                                      fontWeight: '500',
                                      transition: 'all 0.3s ease'
                                    }}
                                    onMouseEnter={(e) => {
                                      e.currentTarget.style.background = '#48c774'
                                      e.currentTarget.style.color = 'white'
                                    }}
                                    onMouseLeave={(e) => {
                                      e.currentTarget.style.background = ''
                                      e.currentTarget.style.color = ''
                                    }}
                                  >
                                    <span className="icon">
                                      <i className="fas fa-plus"></i>
                                    </span>
                                    <span>Add Service</span>
                                  </button>
                                  <button 
                                    className="button is-danger is-light is-small"
                                    onClick={() => deleteSubcategory(category.id, subcategory.id)}
                                    style={{ 
                                      borderRadius: '8px',
                                      transition: 'all 0.3s ease'
                                    }}
                                    onMouseEnter={(e) => {
                                      e.currentTarget.style.background = '#f14668'
                                      e.currentTarget.style.color = 'white'
                                    }}
                                    onMouseLeave={(e) => {
                                      e.currentTarget.style.background = ''
                                      e.currentTarget.style.color = ''
                                    }}
                                  >
                                    <span className="icon">
                                      <i className="fas fa-trash"></i>
                                    </span>
                                  </button>
                                </div>
                              </div>
                            </div>
                          </div>

                          {/* Services */}
                          {expandedSubcategories.includes(subcategory.id) && (
                            <div style={{ marginLeft: '1rem', marginTop: '1rem' }}>
                              {subcategory.services.length === 0 ? (
                                <div style={{ 
                                  padding: '1.5rem',
                                  textAlign: 'center',
                                  background: 'white',
                                  borderRadius: '10px',
                                  border: '2px dashed #ced4da'
                                }}>
                                  <i className="fas fa-box-open" style={{ fontSize: '1.5rem', color: '#adb5bd', marginBottom: '0.5rem' }}></i>
                                  <p style={{ fontStyle: 'italic', color: '#6c757d', margin: 0 }}>No services yet. Add your first service!</p>
                                </div>
                              ) : (
                                <div style={{ overflowX: 'auto' }}>
                                  <table className="table is-fullwidth is-hoverable" style={{ 
                                    background: 'white',
                                    borderRadius: '10px',
                                    overflow: 'hidden',
                                    boxShadow: '0 2px 8px rgba(0, 0, 0, 0.05)'
                                  }}>
                                  <thead>
                                    <tr>
                                      <th>Service Name</th>
                                      <th>Description</th>
                                      <th style={{ width: '150px' }}>Price</th>
                                      <th style={{ width: '120px' }}>Actions</th>
                                    </tr>
                                  </thead>
                                  <tbody>
                                    {subcategory.services.map((service, sIndex) => (
                                      <tr 
                                        key={service.id}
                                        style={{
                                          animation: `fadeIn 0.3s ease ${sIndex * 0.05}s both`,
                                          transition: 'all 0.3s ease'
                                        }}
                                        onMouseEnter={(e) => e.currentTarget.style.background = '#f8f9fa'}
                                        onMouseLeave={(e) => e.currentTarget.style.background = ''}
                                      >
                                        <td className="has-text-weight-semibold" style={{ 
                                          padding: '1rem',
                                          color: '#2c3e50'
                                        }}>
                                          <i className="fas fa-hospital" style={{ marginRight: '0.5rem', color: '#667eea' }}></i>
                                          {service.name}
                                        </td>
                                        <td style={{ color: '#6b7280', padding: '1rem' }}>{service.description || '-'}</td>
                                        <td style={{ padding: '1rem' }}>
                                          <span className="tag is-warning is-medium" style={{ 
                                            borderRadius: '8px',
                                            fontWeight: '700',
                                            fontSize: '1rem',
                                            padding: '0.5rem 1rem'
                                          }}>
                                            {service.price.toFixed(2)}
                                          </span>
                                        </td>
                                        <td style={{ padding: '1rem' }}>
                                          <div className="buttons are-small">
                                            <button 
                                              className="button is-info is-light is-small"
                                              onClick={() => openEditServiceModal(category, subcategory, service)}
                                              style={{ 
                                                borderRadius: '8px',
                                                transition: 'all 0.3s ease'
                                              }}
                                              onMouseEnter={(e) => {
                                                e.currentTarget.style.background = '#3273dc'
                                                e.currentTarget.style.color = 'white'
                                              }}
                                              onMouseLeave={(e) => {
                                                e.currentTarget.style.background = ''
                                                e.currentTarget.style.color = ''
                                              }}
                                            >
                                              <span className="icon">
                                                <i className="fas fa-edit"></i>
                                              </span>
                                            </button>
                                            <button 
                                              className="button is-danger is-light is-small"
                                              onClick={() => deleteService(category.id, subcategory.id, service.id)}
                                              style={{ 
                                                borderRadius: '8px',
                                                transition: 'all 0.3s ease'
                                              }}
                                              onMouseEnter={(e) => {
                                                e.currentTarget.style.background = '#f14668'
                                                e.currentTarget.style.color = 'white'
                                              }}
                                              onMouseLeave={(e) => {
                                                e.currentTarget.style.background = ''
                                                e.currentTarget.style.color = ''
                                              }}
                                            >
                                              <span className="icon">
                                                <i className="fas fa-trash"></i>
                                              </span>
                                            </button>
                                          </div>
                                        </td>
                                      </tr>
                                    ))}
                                  </tbody>
                                </table>
                                </div>
                              )}
                            </div>
                          )}
                        </div>
                      ))
                    )}
                  </div>
                )}
              </div>
            ))}
          </div>
        )}

        {/* Add/Edit Category Modal */}
        {showCategoryModal && (
          <div className="modal is-active">
            <div className="modal-background" onClick={() => setShowCategoryModal(false)} style={{ background: 'rgba(0, 0, 0, 0.6)' }}></div>
            <div className="modal-card" style={{ 
              borderRadius: '16px', 
              overflow: 'hidden',
              animation: 'fadeInUp 0.3s ease'
            }}>
              <header className="modal-card-head" style={{ 
                background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
                borderBottom: 'none'
              }}>
                <p className="modal-card-title" style={{ color: 'white', fontWeight: '600' }}>
                  <i className="fas fa-folder" style={{ marginRight: '0.5rem' }}></i>
                  {editMode === 'category' ? 'Edit Category' : 'Add New Category'}
                </p>
                <button className="delete" onClick={() => setShowCategoryModal(false)} style={{ background: 'rgba(255, 255, 255, 0.3)' }}></button>
              </header>
              <section className="modal-card-body" style={{ padding: '2rem' }}>
                <div className="field">
                  <label className="label" style={{ fontWeight: '600', color: '#2c3e50' }}>Category Name *</label>
                  <div className="control">
                    <input 
                      className="input"
                      type="text"
                      placeholder="e.g., Medical Consultations"
                      value={categoryName}
                      onChange={(e) => setCategoryName(e.target.value)}
                      autoFocus
                      style={{ 
                        borderRadius: '10px',
                        border: '2px solid #e5e7eb',
                        padding: '0.75rem 1rem',
                        fontSize: '1rem',
                        transition: 'all 0.3s ease'
                      }}
                      onFocus={(e) => e.target.style.borderColor = '#667eea'}
                      onBlur={(e) => e.target.style.borderColor = '#e5e7eb'}
                    />
                  </div>
                </div>
              </section>
              <footer className="modal-card-foot" style={{ 
                borderTop: '1px solid #e5e7eb',
                background: '#f9fafb',
                justifyContent: 'flex-end',
                gap: '0.75rem'
              }}>
                <button className="button" onClick={() => setShowCategoryModal(false)} style={{ borderRadius: '10px' }}>Cancel</button>
                <button className="button is-primary" onClick={handleSaveCategory} disabled={!categoryName.trim()} style={{ 
                  borderRadius: '10px',
                  fontWeight: '600'
                }}>
                  <span className="icon">
                    <i className="fas fa-check-circle"></i>
                  </span>
                  <span>{editMode === 'category' ? 'Update' : 'Create'}</span>
                </button>
              </footer>
            </div>
          </div>
        )}

        {/* Add/Edit Subcategory Modal */}
        {showSubcategoryModal && (
          <div className="modal is-active">
            <div className="modal-background" onClick={() => setShowSubcategoryModal(false)} style={{ background: 'rgba(0, 0, 0, 0.6)' }}></div>
            <div className="modal-card" style={{ 
              borderRadius: '16px', 
              overflow: 'hidden',
              animation: 'fadeInUp 0.3s ease'
            }}>
              <header className="modal-card-head" style={{ 
                background: 'linear-gradient(135deg, #10b981 0%, #059669 100%)',
                borderBottom: 'none'
              }}>
                <p className="modal-card-title" style={{ color: 'white', fontWeight: '600' }}>
                  <i className="fas fa-layer-group" style={{ marginRight: '0.5rem' }}></i>
                  {editMode === 'subcategory' ? `Edit Subcategory` : `Add Subcategory`}
                </p>
                <button className="delete" onClick={() => setShowSubcategoryModal(false)} style={{ background: 'rgba(255, 255, 255, 0.3)' }}></button>
              </header>
              <section className="modal-card-body" style={{ padding: '2rem' }}>
                <div className="notification is-light" style={{ 
                  marginBottom: '1.5rem',
                  borderRadius: '10px',
                  background: '#f0fdf4',
                  border: '1px solid #bbf7d0'
                }}>
                  <p style={{ margin: 0, color: '#166534' }}>
                    <i className="fas fa-info-circle" style={{ marginRight: '0.5rem' }}></i>
                    Category: <strong>{selectedCategory?.name}</strong>
                  </p>
                </div>
                <div className="field">
                  <label className="label" style={{ fontWeight: '600', color: '#2c3e50' }}>Subcategory Name *</label>
                  <div className="control">
                    <input 
                      className="input"
                      type="text"
                      placeholder="e.g., General Consultation"
                      value={subcategoryName}
                      onChange={(e) => setSubcategoryName(e.target.value)}
                      autoFocus
                      style={{ 
                        borderRadius: '10px',
                        border: '2px solid #e5e7eb',
                        padding: '0.75rem 1rem',
                        fontSize: '1rem',
                        transition: 'all 0.3s ease'
                      }}
                      onFocus={(e) => e.target.style.borderColor = '#10b981'}
                      onBlur={(e) => e.target.style.borderColor = '#e5e7eb'}
                    />
                  </div>
                </div>
              </section>
              <footer className="modal-card-foot" style={{ 
                borderTop: '1px solid #e5e7eb',
                background: '#f9fafb',
                justifyContent: 'flex-end',
                gap: '0.75rem'
              }}>
                <button className="button" onClick={() => setShowSubcategoryModal(false)} style={{ borderRadius: '10px' }}>Cancel</button>
                <button className="button is-success" onClick={handleSaveSubcategory} disabled={!subcategoryName.trim()} style={{ 
                  borderRadius: '10px',
                  fontWeight: '600'
                }}>
                  <span className="icon">
                    <i className="fas fa-check-circle"></i>
                  </span>
                  <span>{editMode === 'subcategory' ? 'Update' : 'Create'}</span>
                </button>
              </footer>
            </div>
          </div>
        )}

        {/* Add/Edit Service Modal */}
        {showServiceModal && (
          <div className="modal is-active">
            <div className="modal-background" onClick={() => setShowServiceModal(false)} style={{ background: 'rgba(0, 0, 0, 0.6)' }}></div>
            <div className="modal-card" style={{ 
              borderRadius: '16px', 
              overflow: 'hidden',
              animation: 'fadeInUp 0.3s ease',
              maxWidth: '600px'
            }}>
              <header className="modal-card-head" style={{ 
                background: 'linear-gradient(135deg, #f59e0b 0%, #d97706 100%)',
                borderBottom: 'none'
              }}>
                <p className="modal-card-title" style={{ color: 'white', fontWeight: '600' }}>
                  <i className="fas fa-hospital" style={{ marginRight: '0.5rem' }}></i>
                  {editMode === 'service' ? `Edit Service` : `Add Service`}
                </p>
                <button className="delete" onClick={() => setShowServiceModal(false)} style={{ background: 'rgba(255, 255, 255, 0.3)' }}></button>
              </header>
              <section className="modal-card-body" style={{ padding: '2rem' }}>
                <div className="notification is-light" style={{ 
                  marginBottom: '1.5rem',
                  borderRadius: '10px',
                  background: '#fef3c7',
                  border: '1px solid #fde68a'
                }}>
                  <p style={{ margin: 0, color: '#92400e', fontSize: '0.9rem' }}>
                    <i className="fas fa-info-circle" style={{ marginRight: '0.5rem' }}></i>
                    <strong>{selectedCategory?.name}</strong>  <strong>{selectedSubcategory?.name}</strong>
                  </p>
                </div>

                <div className="field">
                  <label className="label" style={{ fontWeight: '600', color: '#2c3e50' }}>
                    <i className="fas fa-stethoscope" style={{ marginRight: '0.5rem', color: '#f59e0b' }}></i>
                    Service Name *
                  </label>
                  <div className="control">
                    <input 
                      className="input"
                      type="text"
                      placeholder="e.g., Regular Checkup"
                      value={serviceName}
                      onChange={(e) => setServiceName(e.target.value)}
                      autoFocus
                      style={{ 
                        borderRadius: '10px',
                        border: '2px solid #e5e7eb',
                        padding: '0.75rem 1rem',
                        fontSize: '1rem',
                        transition: 'all 0.3s ease'
                      }}
                      onFocus={(e) => e.target.style.borderColor = '#f59e0b'}
                      onBlur={(e) => e.target.style.borderColor = '#e5e7eb'}
                    />
                  </div>
                </div>

                <div className="field">
                  <label className="label" style={{ fontWeight: '600', color: '#2c3e50' }}>
                    <i className="fas fa-peso-sign" style={{ marginRight: '0.5rem', color: '#f59e0b' }}></i>
                    Price () *
                  </label>
                  <div className="control">
                    <input 
                      className="input"
                      type="number"
                      step="0.01"
                      placeholder="0.00"
                      value={servicePrice}
                      onChange={(e) => setServicePrice(e.target.value)}
                      style={{ 
                        borderRadius: '10px',
                        border: '2px solid #e5e7eb',
                        padding: '0.75rem 1rem',
                        fontSize: '1rem',
                        transition: 'all 0.3s ease'
                      }}
                      onFocus={(e) => e.target.style.borderColor = '#f59e0b'}
                      onBlur={(e) => e.target.style.borderColor = '#e5e7eb'}
                    />
                  </div>
                </div>

                <div className="field">
                  <label className="label" style={{ fontWeight: '600', color: '#2c3e50' }}>
                    <i className="fas fa-align-left" style={{ marginRight: '0.5rem', color: '#f59e0b' }}></i>
                    Description <span style={{ fontWeight: 'normal', color: '#6b7280' }}>(Optional)</span>
                  </label>
                  <div className="control">
                    <textarea 
                      className="textarea"
                      placeholder="Brief description of the service"
                      value={serviceDescription}
                      onChange={(e) => setServiceDescription(e.target.value)}
                      rows="3"
                      style={{ 
                        borderRadius: '10px',
                        border: '2px solid #e5e7eb',
                        padding: '0.75rem 1rem',
                        fontSize: '1rem',
                        transition: 'all 0.3s ease',
                        resize: 'vertical'
                      }}
                      onFocus={(e) => e.target.style.borderColor = '#f59e0b'}
                      onBlur={(e) => e.target.style.borderColor = '#e5e7eb'}
                    />
                  </div>
                </div>
              </section>
              <footer className="modal-card-foot" style={{ 
                borderTop: '1px solid #e5e7eb',
                background: '#f9fafb',
                justifyContent: 'flex-end',
                gap: '0.75rem'
              }}>
                <button className="button" onClick={() => setShowServiceModal(false)} style={{ borderRadius: '10px' }}>Cancel</button>
                <button 
                  className="button is-warning" 
                  onClick={handleSaveService} 
                  disabled={!serviceName.trim() || !servicePrice}
                  style={{ 
                    borderRadius: '10px',
                    fontWeight: '600'
                  }}
                >
                  <span className="icon">
                    <i className="fas fa-check-circle"></i>
                  </span>
                  <span>{editMode === 'service' ? 'Update' : 'Create'}</span>
                </button>
              </footer>
            </div>
          </div>
        )}
      </div>
    </div>
    </>
  )
}

export default Services
