import React, { useState, useEffect } from 'react'
import { useNavigate, useLocation, Routes, Route } from 'react-router-dom'
import { authService } from '../../../authService'
import RuralHealthHome from './pages/RuralHealthHome'
import Services from './pages/Services'
import OrderOfPayments from './pages/OrderOfPayments'
import RHUSettings from './pages/RHUSettings'
import RHUAccessLogin from './RHUAccessLogin'
import ProfileModal from '../../modals/ProfileModal'
import SettingsModal from '../../modals/SettingsModal'

const RuralHealthDashboard = ({ onLogout }) => {
  const [user, setUser] = useState(null)
  const [accessType, setAccessType] = useState(null) // 'login' or 'code'
  const [showAccessLogin, setShowAccessLogin] = useState(false)
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false)
  const [isDropdownOpen, setIsDropdownOpen] = useState(false)
  const [showProfileModal, setShowProfileModal] = useState(false)
  const [showSettingsModal, setShowSettingsModal] = useState(false)
  const navigate = useNavigate()
  const location = useLocation()

  const menuItems = [
    { name: 'Dashboard', icon: 'fas fa-tachometer-alt', path: '/portal/rural-health/dashboard' },
    { name: 'Services', icon: 'fas fa-clipboard-list', path: '/portal/rural-health/services' },
    { name: 'Order of Payments', icon: 'fas fa-file-invoice-dollar', path: '/portal/rural-health/order-payments' },
    { name: 'Report', icon: 'fas fa-chart-bar', path: '/portal/rural-health/report' },
    { name: 'Settings', icon: 'fas fa-cog', path: '/portal/rural-health/settings' }
  ]

  const getActiveMenu = () => {
    const path = location.pathname
    const menuItem = menuItems.find(item => item.path === path)
    return menuItem ? menuItem.name : 'Dashboard'
  }

  const activeMenu = getActiveMenu()

  useEffect(() => {
    // Check for normal login first
    const currentUser = authService.getUserSession()
    
    if (currentUser) {
      // User is logged in normally
      setUser(currentUser)
      setAccessType('login')
      setShowAccessLogin(false)
    } else {
      // Check for access code session
      const accessSession = sessionStorage.getItem('rhu_access')
      
      if (accessSession) {
        try {
          const access = JSON.parse(accessSession)
          // Create a staff user object
          setUser({
            firstname: 'RHU',
            lastname: 'Staff',
            role: 'RHU Staff',
            accessCode: access.code
          })
          setAccessType('code')
          setShowAccessLogin(false)
        } catch (error) {
          // Invalid session, show access login
          setShowAccessLogin(true)
        }
      } else {
        // No authentication, show access code login
        setShowAccessLogin(true)
      }
    }
  }, [navigate])

  useEffect(() => {
    const handleScroll = () => {
      const header = document.querySelector('.dashboard-header')
      const scrollContainer = document.querySelector('.content-container')
      const scrolled = scrollContainer ? scrollContainer.scrollTop > 10 : window.scrollY > 10
      
      if (header) {
        if (scrolled) {
          header.classList.add('scrolled')
        } else {
          header.classList.remove('scrolled')
        }
      }
    }

    const scrollContainer = document.querySelector('.content-container')
    if (scrollContainer) {
      scrollContainer.addEventListener('scroll', handleScroll)
      return () => scrollContainer.removeEventListener('scroll', handleScroll)
    } else {
      window.addEventListener('scroll', handleScroll)
      return () => window.removeEventListener('scroll', handleScroll)
    }
  }, [])

  useEffect(() => {
    if (location.pathname === '/portal/rural-health' || location.pathname === '/portal/rural-health/') {
      // Redirect based on access type
      if (accessType === 'code') {
        navigate('/portal/rural-health/order-payments', { replace: true })
      } else if (accessType === 'login') {
        navigate('/portal/rural-health/dashboard', { replace: true })
      }
    }
    
    // Prevent code users from accessing dashboard
    if (accessType === 'code' && location.pathname === '/portal/rural-health/dashboard') {
      navigate('/portal/rural-health/order-payments', { replace: true })
    }
  }, [location.pathname, navigate, accessType])

  const handleBackToPortal = () => {
    if (accessType === 'login') {
      navigate('/portal')
    } else {
      // Code users can't go to portal, logout instead
      handleLogout()
    }
  }
  
  const handleAccessGranted = (accessUser) => {
    setUser(accessUser)
    setAccessType('code')
    setShowAccessLogin(false)
    navigate('/portal/rural-health/order-payments')
  }

  useEffect(() => {
    const handleClickOutside = (event) => {
      const dropdown = document.querySelector('.user-dropdown')
      if (dropdown && !dropdown.contains(event.target)) {
        setIsDropdownOpen(false)
      }
    }

    if (isDropdownOpen) {
      document.addEventListener('mousedown', handleClickOutside)
      return () => document.removeEventListener('mousedown', handleClickOutside)
    }
  }, [isDropdownOpen])

  const handleLogout = () => {
    // Clear access code session if exists
    sessionStorage.removeItem('rhu_access')
    
    // Clear normal auth session
    authService.logout()
    
    if (onLogout) {
      onLogout()
    }
    
    // Code users should see access login again, normal users go to login
    if (accessType === 'code') {
      setUser(null)
      setAccessType(null)
      setShowAccessLogin(true)
    } else {
      navigate('/login', { replace: true })
    }
  }

  const handleProfileClick = () => {
    setShowProfileModal(true)
    setIsDropdownOpen(false)
  }

  const handleSettingsClick = () => {
    setShowSettingsModal(true)
    setIsDropdownOpen(false)
  }

  const handleProfileUpdate = (updatedUser) => {
    setUser(updatedUser)
    authService.setUserSession(updatedUser)
  }

  const handleMenuClick = (menuName) => {
    const menuItem = menuItems.find(item => item.name === menuName)
    if (menuItem) {
      navigate(menuItem.path)
    }
    setIsMobileMenuOpen(false)
  }

  // Show access code login if needed
  if (showAccessLogin) {
    return <RHUAccessLogin onAccessGranted={handleAccessGranted} />
  }

  if (!user) {
    return (
      <div className="container">
        <div className="section">
          <div className="has-text-centered">
            <button className="button is-loading is-large">Loading</button>
          </div>
        </div>
      </div>
    )
  }

  // Filter menu items based on access type
  const filteredMenuItems = accessType === 'code' 
    ? menuItems.filter(item => item.name !== 'Dashboard')
    : menuItems

  return (
    <div className="dashboard-container">
      <header className="dashboard-header">
        <div className="header-content">
          <div className="header-left">
            <button 
              className="button is-light is-small"
              onClick={handleBackToPortal}
              style={{ marginRight: '1rem' }}
              title={accessType === 'login' ? 'Back to Systems Portal' : 'Logout'}
            >
              <span className="icon">
                <i className={`fas ${accessType === 'login' ? 'fa-arrow-left' : 'fa-sign-out-alt'}`}></i>
              </span>
              <span>{accessType === 'login' ? 'Portal' : 'Exit'}</span>
            </button>
            <div className="brand-icon" style={{ background: 'linear-gradient(135deg, #ef4444 0%, #dc2626 100%)' }}>
              <i className="fas fa-heartbeat"></i>
            </div>
            <div className="brand-text">
              <h1 className="brand-title">Rural Health Unit</h1>
              <p className="brand-subtitle">LGU Concepcion, Romblon</p>
            </div>
          </div>

          <nav className="header-center">
            <div className="nav-menu">
              {filteredMenuItems.map((item) => (
                <button
                  key={item.name}
                  className={`nav-item ${activeMenu === item.name ? 'active' : ''}`}
                  onClick={() => handleMenuClick(item.name)}
                >
                  <i className={item.icon}></i>
                  <span>{item.name}</span>
                </button>
              ))}
            </div>
          </nav>

          <div className="header-right">
            <div className="user-info">
              <span className="user-name">{user.firstname} {user.lastname}</span>
              <span className="user-role">{user.role}</span>
            </div>
            <div className={`user-dropdown ${isDropdownOpen ? 'is-active' : ''}`}>
              <button 
                className="user-button"
                onClick={() => setIsDropdownOpen(!isDropdownOpen)}
              >
                <div className="user-avatar">
                  {user.firstname.charAt(0)}{user.lastname.charAt(0)}
                </div>
                <i className="fas fa-chevron-down" style={{ fontSize: '0.75rem', color: '#9ca3af' }}></i>
              </button>
              
              <div className="dropdown-menu">
                {accessType === 'login' && (
                  <>
                    <button className="dropdown-item" onClick={handleProfileClick}>
                      <i className="fas fa-user"></i>
                      <span>Profile</span>
                    </button>
                    <button className="dropdown-item" onClick={handleSettingsClick}>
                      <i className="fas fa-cog"></i>
                      <span>Settings</span>
                    </button>
                    <div className="dropdown-divider"></div>
                  </>
                )}
                <button className="dropdown-item logout" onClick={handleLogout}>
                  <i className="fas fa-sign-out-alt"></i>
                  <span>Logout</span>
                </button>
              </div>
            </div>
          </div>

          <button 
            className="mobile-menu-button"
            onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
          >
            <i className={`fas ${isMobileMenuOpen ? 'fa-times' : 'fa-bars'}`}></i>
          </button>
        </div>

        <div className={`mobile-nav ${isMobileMenuOpen ? 'is-active' : ''}`}>
          {filteredMenuItems.map((item) => (
            <button
              key={item.name}
              className={`mobile-nav-item ${activeMenu === item.name ? 'active' : ''}`}
              onClick={() => handleMenuClick(item.name)}
            >
              <i className={item.icon}></i>
              <span>{item.name}</span>
            </button>
          ))}
        </div>
      </header>

      <main className="dashboard-main">
        <div className="content-container">
          <Routes>
            <Route index element={<RuralHealthHome user={user} />} />
            <Route path="dashboard" element={<RuralHealthHome user={user} />} />
            <Route path="services" element={<Services />} />
            <Route path="order-payments" element={<OrderOfPayments />} />
            <Route path="report" element={<div className="section"><h1 className="title">Report - Coming Soon</h1></div>} />
            <Route path="settings" element={<RHUSettings user={user} />} />
          </Routes>
        </div>
      </main>

      <ProfileModal
        user={user}
        isOpen={showProfileModal}
        onClose={() => setShowProfileModal(false)}
        onUpdate={handleProfileUpdate}
      />
      <SettingsModal
        isOpen={showSettingsModal}
        onClose={() => setShowSettingsModal(false)}
      />
    </div>
  )
}

export default RuralHealthDashboard
