import React, { useState, useEffect } from 'react'
import { useNavigate, useLocation, Routes, Route } from 'react-router-dom'
import { authService } from '../../../authService'
import DashboardHome from '../../DashboardHome'
import CustomersPage from '../../pages/CustomersPage'
import BillingPage from '../../pages/BillingPage'
import PaymentsPage from '../../pages/PaymentsPage'
import CreditManagementPage from '../../pages/CreditManagementPage'
import ReportsPage from '../../pages/ReportsPage'
import UsersPage from '../../pages/UsersPage'
import ToolsPage from '../../pages/ToolsPage'
import ProfileModal from '../../modals/ProfileModal'
import SettingsModal from '../../modals/SettingsModal'

const WaterBillingDashboard = ({ onLogout }) => {
  const [user, setUser] = useState(null)
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false)
  const [isDropdownOpen, setIsDropdownOpen] = useState(false)
  const [showProfileModal, setShowProfileModal] = useState(false)
  const [showSettingsModal, setShowSettingsModal] = useState(false)
  const navigate = useNavigate()
  const location = useLocation()

  const menuItems = [
    { name: 'Dashboard', icon: 'fas fa-tachometer-alt', path: '/portal/water-billing/dashboard' },
    { name: 'Customers', icon: 'fas fa-users', path: '/portal/water-billing/customers' },
    { name: 'Billing', icon: 'fas fa-file-invoice-dollar', path: '/portal/water-billing/billing' },
    { name: 'Payments', icon: 'fas fa-credit-card', path: '/portal/water-billing/payments' },
    { name: 'Credit Management', icon: 'fas fa-chart-line', path: '/portal/water-billing/credit-management' },
    { name: 'Reports', icon: 'fas fa-chart-bar', path: '/portal/water-billing/reports' },
    { name: 'Users', icon: 'fas fa-user-cog', path: '/portal/water-billing/users' },
    { name: 'Tools', icon: 'fas fa-tools', path: '/portal/water-billing/tools' }
  ]

  // Get current active menu based on URL
  const getActiveMenu = () => {
    const path = location.pathname
    const menuItem = menuItems.find(item => item.path === path)
    return menuItem ? menuItem.name : 'Dashboard'
  }

  const activeMenu = getActiveMenu()

  useEffect(() => {
    const currentUser = authService.getUserSession()
    if (!currentUser) {
      navigate('/login')
    } else {
      setUser(currentUser)
    }
  }, [navigate])

  // Add scroll detection for header styling
  useEffect(() => {
    const handleScroll = () => {
      const header = document.querySelector('.dashboard-header')
      const scrollContainer = document.querySelector('.content-container')
      const scrolled = scrollContainer ? scrollContainer.scrollTop > 10 : window.scrollY > 10
      
      if (header) {
        if (scrolled) {
          header.classList.add('scrolled')
        } else {
          header.classList.remove('scrolled')
        }
      }
    }

    const scrollContainer = document.querySelector('.content-container')
    if (scrollContainer) {
      scrollContainer.addEventListener('scroll', handleScroll)
      return () => scrollContainer.removeEventListener('scroll', handleScroll)
    } else {
      window.addEventListener('scroll', handleScroll)
      return () => window.removeEventListener('scroll', handleScroll)
    }
  }, [])

  // Redirect to dashboard home if on exact path
  useEffect(() => {
    if (location.pathname === '/portal/water-billing') {
      navigate('/portal/water-billing/dashboard', { replace: true })
    }
  }, [location.pathname, navigate])

  const handleBackToPortal = () => {
    navigate('/portal')
  }

  // Close dropdown when clicking outside
  useEffect(() => {
    const handleClickOutside = (event) => {
      const dropdown = document.querySelector('.user-dropdown')
      if (dropdown && !dropdown.contains(event.target)) {
        setIsDropdownOpen(false)
      }
    }

    if (isDropdownOpen) {
      document.addEventListener('mousedown', handleClickOutside)
      return () => document.removeEventListener('mousedown', handleClickOutside)
    }
  }, [isDropdownOpen])

  // Listen for navigation to payments event from BillModal
  useEffect(() => {
    const handleNavigateToPayments = (event) => {
      navigate('/portal/water-billing/payments')
    }

    window.addEventListener('navigateToPayments', handleNavigateToPayments)
    return () => window.removeEventListener('navigateToPayments', handleNavigateToPayments)
  }, [navigate])

  const handleLogout = () => {
    authService.logout()
    if (onLogout) {
      onLogout()
    }
    navigate('/login', { replace: true })
  }

  const handleProfileClick = () => {
    setShowProfileModal(true)
    setIsDropdownOpen(false)
  }

  const handleSettingsClick = () => {
    setShowSettingsModal(true)
    setIsDropdownOpen(false)
  }

  const handleProfileUpdate = (updatedUser) => {
    setUser(updatedUser)
    authService.setUserSession(updatedUser)
  }

  const handleMenuClick = (menuName) => {
    const menuItem = menuItems.find(item => item.name === menuName)
    if (menuItem) {
      navigate(menuItem.path)
    }
    setIsMobileMenuOpen(false)
  }

  if (!user) {
    return (
      <div className="container">
        <div className="section">
          <div className="has-text-centered">
            <button className="button is-loading is-large">Loading</button>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="dashboard-container">
      {/* Professional Header */}
      <header className="dashboard-header">
        <div className="header-content">
          {/* Left: Logo, Title and Description */}
          <div className="header-left">
            <button 
              className="button is-light is-small"
              onClick={handleBackToPortal}
              style={{ marginRight: '1rem' }}
              title="Back to Systems Portal"
            >
              <span className="icon">
                <i className="fas fa-arrow-left"></i>
              </span>
              <span>Portal</span>
            </button>
            <div className="brand-icon">
              <i className="fas fa-tint"></i>
            </div>
            <div className="brand-text">
              <h1 className="brand-title">Water Billing System</h1>
              <p className="brand-subtitle">LGU Concepcion, Romblon</p>
            </div>
          </div>

          {/* Center: Navigation Menu */}
          <nav className="header-center">
            <div className="nav-menu">
              {menuItems.map((item) => (
                <button
                  key={item.name}
                  className={`nav-item ${activeMenu === item.name ? 'active' : ''}`}
                  onClick={() => handleMenuClick(item.name)}
                >
                  <i className={item.icon}></i>
                  <span>{item.name}</span>
                </button>
              ))}
            </div>
          </nav>

          {/* Right: User Profile */}
          <div className="header-right">
            <div className="user-info">
              <span className="user-name">{user.firstname} {user.lastname}</span>
              <span className="user-role">{user.role}</span>
            </div>
            <div className={`user-dropdown ${isDropdownOpen ? 'is-active' : ''}`}>
              <button 
                className="user-button"
                onClick={() => setIsDropdownOpen(!isDropdownOpen)}
              >
                <div className="user-avatar">
                  {user.firstname.charAt(0)}{user.lastname.charAt(0)}
                </div>
                <i className="fas fa-chevron-down" style={{ fontSize: '0.75rem', color: '#9ca3af' }}></i>
              </button>
              
              <div className="dropdown-menu">
                <button className="dropdown-item" onClick={handleProfileClick}>
                  <i className="fas fa-user"></i>
                  <span>Profile</span>
                </button>
                <button className="dropdown-item" onClick={handleSettingsClick}>
                  <i className="fas fa-cog"></i>
                  <span>Settings</span>
                </button>
                <div className="dropdown-divider"></div>
                <button className="dropdown-item logout" onClick={handleLogout}>
                  <i className="fas fa-sign-out-alt"></i>
                  <span>Logout</span>
                </button>
              </div>
            </div>
          </div>

          {/* Mobile Menu Button */}
          <button 
            className="mobile-menu-button"
            onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
          >
            <i className={`fas ${isMobileMenuOpen ? 'fa-times' : 'fa-bars'}`}></i>
          </button>
        </div>

        {/* Mobile Navigation */}
        <div className={`mobile-nav ${isMobileMenuOpen ? 'is-active' : ''}`}>
          {menuItems.map((item) => (
            <button
              key={item.name}
              className={`mobile-nav-item ${activeMenu === item.name ? 'active' : ''}`}
              onClick={() => handleMenuClick(item.name)}
            >
              <i className={item.icon}></i>
              <span>{item.name}</span>
            </button>
          ))}
        </div>
      </header>

      {/* Main Content */}
      <main className="dashboard-main">
        <div className="content-container">
          <Routes>
            <Route index element={<DashboardHome user={user} />} />
            <Route path="dashboard" element={<DashboardHome user={user} />} />
            <Route path="customers" element={<CustomersPage />} />
            <Route path="billing" element={<BillingPage />} />
            <Route path="payments" element={<PaymentsPage />} />
            <Route path="credit-management" element={<CreditManagementPage />} />
            <Route path="reports" element={<ReportsPage />} />
            <Route path="users" element={<UsersPage />} />
            <Route path="tools" element={<ToolsPage />} />
          </Routes>
        </div>
      </main>

      {/* Modals */}
      <ProfileModal
        user={user}
        isOpen={showProfileModal}
        onClose={() => setShowProfileModal(false)}
        onUpdate={handleProfileUpdate}
      />
      <SettingsModal
        isOpen={showSettingsModal}
        onClose={() => setShowSettingsModal(false)}
      />
    </div>
  )
}

export default WaterBillingDashboard
