import React, { useState } from 'react'
import { rhuService } from '../../../services/rhuService'

const RHUAccessLogin = ({ onAccessGranted }) => {
  const [code, setCode] = useState('')
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')

  const handleSubmit = async (e) => {
    e.preventDefault()
    
    if (!code.trim()) {
      setError('Please enter an access code')
      return
    }

    setLoading(true)
    setError('')

    const result = await rhuService.verifyAccessCode(code)

    if (result.success) {
      // Store access code session
      sessionStorage.setItem('rhu_access', JSON.stringify({
        code: code,
        accessType: 'code',
        timestamp: new Date().toISOString()
      }))
      
      onAccessGranted({
        accessType: 'code',
        firstname: 'RHU',
        lastname: 'Staff',
        role: 'RHU Staff'
      })
    } else {
      setError(result.error || 'Invalid access code')
      setLoading(false)
    }
  }

  return (
    <div style={{
      minHeight: '100vh',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
      padding: '1rem'
    }}>
      <div style={{
        background: 'white',
        borderRadius: '24px',
        padding: '3rem',
        maxWidth: '450px',
        width: '100%',
        boxShadow: '0 20px 60px rgba(0, 0, 0, 0.3)',
        animation: 'fadeInUp 0.5s ease'
      }}>
        <div style={{ textAlign: 'center', marginBottom: '2rem' }}>
          <div style={{
            width: '80px',
            height: '80px',
            background: 'linear-gradient(135deg, #ef4444 0%, #dc2626 100%)',
            borderRadius: '20px',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            margin: '0 auto 1.5rem',
            boxShadow: '0 10px 30px rgba(239, 68, 68, 0.3)'
          }}>
            <i className="fas fa-heartbeat" style={{ fontSize: '2.5rem', color: 'white' }}></i>
          </div>
          <h1 style={{
            fontSize: '1.75rem',
            fontWeight: '700',
            color: '#1f2937',
            marginBottom: '0.5rem'
          }}>
            Rural Health Unit
          </h1>
          <p style={{
            color: '#6b7280',
            fontSize: '1rem'
          }}>
            LGU Concepcion, Romblon
          </p>
        </div>

        <form onSubmit={handleSubmit}>
          <div style={{ marginBottom: '1.5rem' }}>
            <label style={{
              display: 'block',
              color: '#374151',
              fontWeight: '600',
              marginBottom: '0.5rem',
              fontSize: '0.875rem',
              textTransform: 'uppercase',
              letterSpacing: '0.05em'
            }}>
              Access Code
            </label>
            <input
              type="text"
              className="input is-large"
              value={code}
              onChange={(e) => setCode(e.target.value.toUpperCase())}
              placeholder="Enter access code"
              autoFocus
              style={{
                borderRadius: '12px',
                border: '2px solid #e5e7eb',
                padding: '0.875rem 1rem',
                fontSize: '1.125rem',
                fontWeight: '600',
                textAlign: 'center',
                letterSpacing: '0.1em',
                transition: 'all 0.3s ease',
                textTransform: 'uppercase'
              }}
              onFocus={(e) => e.target.style.borderColor = '#667eea'}
              onBlur={(e) => e.target.style.borderColor = '#e5e7eb'}
            />
            <p style={{
              marginTop: '0.5rem',
              fontSize: '0.875rem',
              color: '#6b7280',
              textAlign: 'center'
            }}>
              Enter your RHU staff access code
            </p>
          </div>

          {error && (
            <div style={{
              background: '#fee2e2',
              border: '2px solid #fecaca',
              borderRadius: '12px',
              padding: '1rem',
              marginBottom: '1.5rem',
              display: 'flex',
              alignItems: 'center',
              gap: '0.75rem'
            }}>
              <i className="fas fa-exclamation-circle" style={{ color: '#dc2626', fontSize: '1.25rem' }}></i>
              <span style={{ color: '#991b1b', fontSize: '0.875rem', fontWeight: '500' }}>
                {error}
              </span>
            </div>
          )}

          <button
            type="submit"
            className="button is-large"
            disabled={loading || !code.trim()}
            style={{
              width: '100%',
              background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
              color: 'white',
              border: 'none',
              borderRadius: '12px',
              padding: '0.875rem',
              fontSize: '1rem',
              fontWeight: '600',
              cursor: loading || !code.trim() ? 'not-allowed' : 'pointer',
              opacity: loading || !code.trim() ? 0.6 : 1,
              transition: 'all 0.3s ease',
              boxShadow: '0 4px 12px rgba(102, 126, 234, 0.4)'
            }}
            onMouseEnter={(e) => {
              if (!loading && code.trim()) {
                e.target.style.transform = 'translateY(-2px)'
                e.target.style.boxShadow = '0 6px 20px rgba(102, 126, 234, 0.6)'
              }
            }}
            onMouseLeave={(e) => {
              e.target.style.transform = 'translateY(0)'
              e.target.style.boxShadow = '0 4px 12px rgba(102, 126, 234, 0.4)'
            }}
          >
            {loading ? (
              <>
                <span className="icon">
                  <i className="fas fa-spinner fa-spin"></i>
                </span>
                <span>Verifying...</span>
              </>
            ) : (
              <>
                <span className="icon">
                  <i className="fas fa-sign-in-alt"></i>
                </span>
                <span>Access System</span>
              </>
            )}
          </button>
        </form>

        <div style={{
          marginTop: '2rem',
          padding: '1rem',
          background: '#f9fafb',
          borderRadius: '12px',
          border: '1px solid #e5e7eb'
        }}>
          <p style={{
            fontSize: '0.75rem',
            color: '#6b7280',
            textAlign: 'center',
            margin: 0,
            lineHeight: '1.5'
          }}>
            <i className="fas fa-info-circle" style={{ marginRight: '0.5rem' }}></i>
            This access is for RHU staff only. Contact your administrator if you need an access code.
          </p>
        </div>
      </div>

      <style>{`
        @keyframes fadeInUp {
          from {
            opacity: 0;
            transform: translateY(30px);
          }
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }
      `}</style>
    </div>
  )
}

export default RHUAccessLogin
