import React, { useState, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import { customerService } from '../services/customerService'
import { billingService } from '../services/billingService'
import { paymentService } from '../services/paymentService'

const DashboardHome = ({ user }) => {
  const navigate = useNavigate()
  const [recentCustomers, setRecentCustomers] = useState([])
  const [recentBills, setRecentBills] = useState([])
  const [recentPayments, setRecentPayments] = useState([])
  const [loading, setLoading] = useState(true)
  const [statsLoading, setStatsLoading] = useState(true)
  const [selectedCustomer, setSelectedCustomer] = useState(null)
  const [selectedBill, setSelectedBill] = useState(null)
  const [selectedPayment, setSelectedPayment] = useState(null)
  const [stats, setStats] = useState({
    totalCustomers: 0,
    activeCustomers: 0,
    monthlyRevenue: 0,
    pendingBills: 0,
    overdueAccounts: 0,
    todayCollection: 0
  })
  
  useEffect(() => {
    loadRecentData()
    loadStatistics()
  }, [])
  
  const loadRecentData = async () => {
    setLoading(true)
    try {
      const [customersResult, billsResult, paymentsResult] = await Promise.all([
        customerService.getRecentCustomers(5),
        billingService.getRecentBills(5),
        paymentService.getRecentPayments(5)
      ])
      
      if (customersResult.success) setRecentCustomers(customersResult.data || [])
      if (billsResult.success) setRecentBills(billsResult.data || [])
      if (paymentsResult.success) setRecentPayments(paymentsResult.data || [])
    } catch (err) {
      console.error('Error loading recent data:', err)
    }
    setLoading(false)
  }

  const loadStatistics = async () => {
    setStatsLoading(true)
    try {
      const [customerStats, paymentStats, billsResult] = await Promise.all([
        customerService.getCustomerStats(),
        paymentService.getPaymentStats(),
        billingService.getBills(1, 10000, 'paymentstatus', true)
      ])

      const newStats = {
        totalCustomers: customerStats.data?.total || 0,
        activeCustomers: customerStats.data?.active || 0,
        monthlyRevenue: paymentStats.data?.monthCollection || 0,
        todayCollection: paymentStats.data?.todayCollection || 0,
        pendingBills: billsResult.data?.filter(b => b.paymentstatus === 'Unpaid').length || 0,
        overdueAccounts: 0 // Will be calculated
      }

      // Calculate overdue accounts (bills from 2+ months ago still unpaid)
      const twoMonthsAgo = new Date()
      twoMonthsAgo.setMonth(twoMonthsAgo.getMonth() - 2)
      const overdueCustomerIds = new Set(
        billsResult.data
          ?.filter(b => b.paymentstatus === 'Unpaid' && new Date(b.billedmonth) < twoMonthsAgo)
          .map(b => b.customerid) || []
      )
      newStats.overdueAccounts = overdueCustomerIds.size

      setStats(newStats)
    } catch (err) {
      console.error('Error loading statistics:', err)
    }
    setStatsLoading(false)
  }
  return (
    <div className="dashboard-content">
      <div className="page-header">
        <h1 className="page-title">Dashboard</h1>
        <p className="page-subtitle">Welcome back, {user.firstname}! Here's your system overview.</p>
      </div>

      {/* Statistics Cards */}
      <div className="stats-grid">
        <div className="stat-card blue">
          <div className="stat-icon">
            <i className="fas fa-users"></i>
          </div>
          <div className="stat-content">
            <div className="stat-number">
              {statsLoading ? <i className="fas fa-spinner fa-pulse"></i> : stats.totalCustomers.toLocaleString()}
            </div>
            <div className="stat-label">Total Customers</div>
            <div className="stat-sublabel">{stats.activeCustomers} Active</div>
          </div>
        </div>
        <div className="stat-card green">
          <div className="stat-icon">
            <i className="fas fa-peso-sign"></i>
          </div>
          <div className="stat-content">
            <div className="stat-number">
              {statsLoading ? <i className="fas fa-spinner fa-pulse"></i> : `${stats.monthlyRevenue.toLocaleString('en-PH', { minimumFractionDigits: 2, maximumFractionDigits: 2 })}`}
            </div>
            <div className="stat-label">Monthly Revenue</div>
            <div className="stat-sublabel">{stats.todayCollection.toLocaleString('en-PH', { minimumFractionDigits: 2 })} Today</div>
          </div>
        </div>
        <div className="stat-card orange">
          <div className="stat-icon">
            <i className="fas fa-file-invoice"></i>
          </div>
          <div className="stat-content">
            <div className="stat-number">
              {statsLoading ? <i className="fas fa-spinner fa-pulse"></i> : stats.pendingBills.toLocaleString()}
            </div>
            <div className="stat-label">Pending Bills</div>
            <div className="stat-sublabel">Awaiting Payment</div>
          </div>
        </div>
        <div className="stat-card red">
          <div className="stat-icon">
            <i className="fas fa-exclamation-circle"></i>
          </div>
          <div className="stat-content">
            <div className="stat-number">
              {statsLoading ? <i className="fas fa-spinner fa-pulse"></i> : stats.overdueAccounts.toLocaleString()}
            </div>
            <div className="stat-label">Overdue Accounts</div>
            <div className="stat-sublabel">2+ Months Behind</div>
          </div>
        </div>
      </div>

      {/* Quick Actions */}
      <div className="box" style={{ marginTop: '2rem', padding: '1.5rem' }}>
        <h2 className="title is-5" style={{ marginBottom: '1rem' }}>
          <span className="icon-text">
            <span className="icon has-text-primary">
              <i className="fas fa-bolt"></i>
            </span>
            <span>Quick Actions</span>
          </span>
        </h2>
        <div className="buttons">
          <button className="button is-primary" onClick={() => navigate('/dashboard/customers')}>
            <span className="icon">
              <i className="fas fa-user-plus"></i>
            </span>
            <span>Add Customer</span>
          </button>
          <button className="button is-info" onClick={() => navigate('/dashboard/billing')}>
            <span className="icon">
              <i className="fas fa-file-invoice-dollar"></i>
            </span>
            <span>Create Bill</span>
          </button>
          <button className="button is-success" onClick={() => navigate('/dashboard/payments')}>
            <span className="icon">
              <i className="fas fa-cash-register"></i>
            </span>
            <span>Record Payment</span>
          </button>
          <button className="button is-link" onClick={() => navigate('/dashboard/reports')}>
            <span className="icon">
              <i className="fas fa-chart-line"></i>
            </span>
            <span>View Reports</span>
          </button>
          <button className="button is-warning" onClick={() => loadStatistics()}>
            <span className="icon">
              <i className={`fas fa-sync-alt ${statsLoading ? 'fa-spin' : ''}`}></i>
            </span>
            <span>Refresh Stats</span>
          </button>
        </div>
      </div>

      {/* Recent Activities Section */}
      <div className="columns is-gapless" style={{ marginTop: '2rem', gap: '0.5rem' }}>
        {/* Recent Customers */}
        <div className="column is-4">
          <div className="card" style={{ boxShadow: '0 2px 8px rgba(0,0,0,0.1)', borderRadius: '6px', overflow: 'hidden' }}>
            <header className="card-header has-background-primary">
              <p className="card-header-title has-text-white">
                <span className="icon"><i className="fas fa-users"></i></span>
                <span>Recent Customers</span>
              </p>
            </header>
            <div className="card-content" style={{ padding: 0 }}>
              {loading ? (
                <div className="has-text-centered" style={{ padding: '2rem' }}>
                  <span className="icon is-large has-text-grey-light">
                    <i className="fas fa-spinner fa-pulse fa-2x"></i>
                  </span>
                </div>
              ) : recentCustomers.length === 0 ? (
                <div className="has-text-centered has-text-grey" style={{ padding: '2rem' }}>
                  <i className="fas fa-inbox" style={{ fontSize: '2rem', marginBottom: '1rem', display: 'block' }}></i>
                  No recent customers
                </div>
              ) : (
                <div style={{ width: '100%' }}>
                  {recentCustomers.map((customer, index) => (
                    <div 
                      key={customer.customerid} 
                      style={{ 
                        cursor: 'pointer', 
                        padding: '0.875rem 0.25rem',
                        borderBottom: index < recentCustomers.length - 1 ? '1px solid #f0f0f0' : 'none',
                        transition: 'all 0.2s ease',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'space-between'
                      }}
                      onClick={() => setSelectedCustomer(customer)}
                      onMouseEnter={(e) => e.currentTarget.style.backgroundColor = '#f8f9fa'}
                      onMouseLeave={(e) => e.currentTarget.style.backgroundColor = 'transparent'}
                    >
                      <div style={{ flex: 1 }}>
                        <div style={{ display: 'flex', alignItems: 'center', marginBottom: '0.4rem' }}>
                          <span className="icon has-text-primary" style={{ marginRight: '0.5rem' }}>
                            <i className="fas fa-user"></i>
                          </span>
                          <strong style={{ fontSize: '0.95rem', color: '#363636' }}>{customer.name}</strong>
                        </div>
                        <div style={{ paddingLeft: '1rem', display: 'flex', alignItems: 'center', gap: '0.5rem' }}>
                          <span className="tag is-primary is-light is-small">{customer.type}</span>
                          <span style={{ fontSize: '0.8rem', color: '#7a7a7a' }}>
                            <i className="fas fa-map-marker-alt" style={{ marginRight: '0.25rem' }}></i>
                            {customer.barangay}
                          </span>
                        </div>
                      </div>
                      <span className="icon has-text-grey-light">
                        <i className="fas fa-chevron-right"></i>
                      </span>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </div>
        </div>

        {/* Recent Bills */}
        <div className="column is-4">
          <div className="card" style={{ boxShadow: '0 2px 8px rgba(0,0,0,0.1)', borderRadius: '6px', overflow: 'hidden' }}>
            <header className="card-header has-background-info">
              <p className="card-header-title has-text-white">
                <span className="icon"><i className="fas fa-file-invoice-dollar"></i></span>
                <span>Recent Bills</span>
              </p>
            </header>
            <div className="card-content" style={{ padding: 0 }}>
              {loading ? (
                <div className="has-text-centered" style={{ padding: '2rem' }}>
                  <span className="icon is-large has-text-grey-light">
                    <i className="fas fa-spinner fa-pulse fa-2x"></i>
                  </span>
                </div>
              ) : recentBills.length === 0 ? (
                <div className="has-text-centered has-text-grey" style={{ padding: '2rem' }}>
                  <i className="fas fa-inbox" style={{ fontSize: '2rem', marginBottom: '1rem', display: 'block' }}></i>
                  No recent bills
                </div>
              ) : (
                <div style={{ width: '100%' }}>
                  {recentBills.map((bill, index) => (
                    <div 
                      key={bill.billid} 
                      style={{ 
                        cursor: 'pointer', 
                        padding: '0.875rem 0.25rem',
                        borderBottom: index < recentBills.length - 1 ? '1px solid #f0f0f0' : 'none',
                        transition: 'all 0.2s ease',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'space-between'
                      }}
                      onClick={() => setSelectedBill(bill)}
                      onMouseEnter={(e) => e.currentTarget.style.backgroundColor = '#f8f9fa'}
                      onMouseLeave={(e) => e.currentTarget.style.backgroundColor = 'transparent'}
                    >
                      <div style={{ flex: 1 }}>
                        <div style={{ display: 'flex', alignItems: 'center', marginBottom: '0.4rem' }}>
                          <span className="icon has-text-info" style={{ marginRight: '0.5rem' }}>
                            <i className="fas fa-file-invoice"></i>
                          </span>
                          <strong style={{ fontSize: '0.95rem', color: '#363636' }}>{bill.customers?.name || bill.customername}</strong>
                        </div>
                        <div style={{ paddingLeft: '1rem', display: 'flex', alignItems: 'center', gap: '0.5rem' }}>
                          <span style={{ fontSize: '0.8rem', color: '#7a7a7a' }}>
                            <i className="fas fa-calendar-alt" style={{ marginRight: '0.25rem' }}></i>
                            {bill.billedmonth}
                          </span>
                          <span className={`tag is-small ${bill.paymentstatus === 'Paid' ? 'is-success' : 'is-warning'}`}>
                            {bill.paymentstatus}
                          </span>
                        </div>
                      </div>
                      <div style={{ textAlign: 'right' }}>
                        <div style={{ fontSize: '1rem', fontWeight: 'bold', color: '#209cee' }}>{bill.basicamount?.toFixed(2)}</div>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </div>
        </div>

        {/* Recent Payments */}
        <div className="column is-4">
          <div className="card" style={{ boxShadow: '0 2px 8px rgba(0,0,0,0.1)', borderRadius: '6px', overflow: 'hidden' }}>
            <header className="card-header has-background-success">
              <p className="card-header-title has-text-white">
                <span className="icon"><i className="fas fa-credit-card"></i></span>
                <span>Recent Payments</span>
              </p>
            </header>
            <div className="card-content" style={{ padding: 0 }}>
              {loading ? (
                <div className="has-text-centered" style={{ padding: '2rem' }}>
                  <span className="icon is-large has-text-grey-light">
                    <i className="fas fa-spinner fa-pulse fa-2x"></i>
                  </span>
                </div>
              ) : recentPayments.length === 0 ? (
                <div className="has-text-centered has-text-grey" style={{ padding: '2rem' }}>
                  <i className="fas fa-inbox" style={{ fontSize: '2rem', marginBottom: '1rem', display: 'block' }}></i>
                  No recent payments
                </div>
              ) : (
                <div style={{ width: '100%' }}>
                  {recentPayments.map((payment, index) => (
                    <div 
                      key={payment.billid} 
                      style={{ 
                        cursor: 'pointer', 
                        padding: '0.875rem 0.25rem',
                        borderBottom: index < recentPayments.length - 1 ? '1px solid #f0f0f0' : 'none',
                        transition: 'all 0.2s ease',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'space-between'
                      }}
                      onClick={() => setSelectedPayment(payment)}
                      onMouseEnter={(e) => e.currentTarget.style.backgroundColor = '#f8f9fa'}
                      onMouseLeave={(e) => e.currentTarget.style.backgroundColor = 'transparent'}
                    >
                      <div style={{ flex: 1 }}>
                        <div style={{ display: 'flex', alignItems: 'center', marginBottom: '0.4rem' }}>
                          <span className="icon has-text-success" style={{ marginRight: '0.5rem' }}>
                            <i className="fas fa-check-circle"></i>
                          </span>
                          <strong style={{ fontSize: '0.95rem', color: '#363636' }}>{payment.customer?.name || 'Unknown'}</strong>
                        </div>
                        <div style={{ paddingLeft: '1rem', display: 'flex', alignItems: 'center', gap: '0.5rem' }}>
                          <span style={{ fontSize: '0.8rem', color: '#7a7a7a' }}>
                            <i className="fas fa-clock" style={{ marginRight: '0.25rem' }}></i>
                            {new Date(payment.datepaid).toLocaleDateString()}
                          </span>
                        </div>
                      </div>
                      <div style={{ textAlign: 'right' }}>
                        <div style={{ fontSize: '1rem', fontWeight: 'bold', color: '#48c774' }}>{payment.totalbillamount?.toFixed(2)}</div>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </div>
        </div>
      </div>

      {/* Customer Detail Modal */}
      {selectedCustomer && (
        <div className="modal is-active">
          <div className="modal-background" onClick={() => setSelectedCustomer(null)}></div>
          <div className="modal-card">
            <header className="modal-card-head">
              <p className="modal-card-title">Customer Details</p>
              <button className="delete" aria-label="close" onClick={() => setSelectedCustomer(null)}></button>
            </header>
            <section className="modal-card-body">
              <div className="content">
                <div className="field">
                  <label className="label">Customer ID</label>
                  <div className="control">
                    <input className="input" type="text" value={selectedCustomer.customerid} readOnly />
                  </div>
                </div>
                <div className="field">
                  <label className="label">Name</label>
                  <div className="control">
                    <input className="input" type="text" value={selectedCustomer.name} readOnly />
                  </div>
                </div>
                <div className="field">
                  <label className="label">Type</label>
                  <div className="control">
                    <input className="input" type="text" value={selectedCustomer.type || 'N/A'} readOnly />
                  </div>
                </div>
                <div className="field">
                  <label className="label">Barangay</label>
                  <div className="control">
                    <input className="input" type="text" value={selectedCustomer.barangay || 'N/A'} readOnly />
                  </div>
                </div>
                <div className="field">
                  <label className="label">Status</label>
                  <div className="control">
                    <span className={`tag ${selectedCustomer.status === 'Active' ? 'is-success' : 'is-danger'}`}>
                      {selectedCustomer.status || 'N/A'}
                    </span>
                  </div>
                </div>
                <div className="field">
                  <label className="label">Date Added</label>
                  <div className="control">
                    <input className="input" type="text" value={selectedCustomer.date_added || 'N/A'} readOnly />
                  </div>
                </div>
              </div>
            </section>
            <footer className="modal-card-foot">
              <button className="button" onClick={() => setSelectedCustomer(null)}>Close</button>
            </footer>
          </div>
        </div>
      )}

      {/* Bill Detail Modal */}
      {selectedBill && (
        <div className="modal is-active">
          <div className="modal-background" onClick={() => setSelectedBill(null)}></div>
          <div className="modal-card">
            <header className="modal-card-head">
              <p className="modal-card-title">Bill Details</p>
              <button className="delete" aria-label="close" onClick={() => setSelectedBill(null)}></button>
            </header>
            <section className="modal-card-body">
              <div className="content">
                <div className="field">
                  <label className="label">Bill ID</label>
                  <div className="control">
                    <input className="input" type="text" value={selectedBill.billid} readOnly />
                  </div>
                </div>
                <div className="field">
                  <label className="label">Customer</label>
                  <div className="control">
                    <input className="input" type="text" value={selectedBill.customers?.name || selectedBill.customername} readOnly />
                  </div>
                </div>
                <div className="field">
                  <label className="label">Billed Month</label>
                  <div className="control">
                    <input className="input" type="text" value={selectedBill.billedmonth} readOnly />
                  </div>
                </div>
                <div className="columns">
                  <div className="column">
                    <div className="field">
                      <label className="label">Previous Reading</label>
                      <div className="control">
                        <input className="input" type="text" value={selectedBill.previousreading || 0} readOnly />
                      </div>
                    </div>
                  </div>
                  <div className="column">
                    <div className="field">
                      <label className="label">Current Reading</label>
                      <div className="control">
                        <input className="input" type="text" value={selectedBill.currentreading || 0} readOnly />
                      </div>
                    </div>
                  </div>
                  <div className="column">
                    <div className="field">
                      <label className="label">Consumption</label>
                      <div className="control">
                        <input className="input" type="text" value={selectedBill.consumption || 0} readOnly />
                      </div>
                    </div>
                  </div>
                </div>
                <div className="columns">
                  <div className="column">
                    <div className="field">
                      <label className="label">Basic Amount</label>
                      <div className="control">
                        <input className="input" type="text" value={`${selectedBill.basicamount?.toFixed(2) || '0.00'}`} readOnly />
                      </div>
                    </div>
                  </div>
                  <div className="column">
                    <div className="field">
                      <label className="label">Surcharge</label>
                      <div className="control">
                        <input className="input" type="text" value={`${selectedBill.surchargeamount?.toFixed(2) || '0.00'}`} readOnly />
                      </div>
                    </div>
                  </div>
                </div>
                <div className="field">
                  <label className="label">Total Amount</label>
                  <div className="control">
                    <input className="input has-text-weight-bold" type="text" value={`${selectedBill.totalbillamount?.toFixed(2) || '0.00'}`} readOnly />
                  </div>
                </div>
                <div className="field">
                  <label className="label">Payment Status</label>
                  <div className="control">
                    <span className={`tag ${selectedBill.paymentstatus === 'Paid' ? 'is-success' : 'is-warning'}`}>
                      {selectedBill.paymentstatus}
                    </span>
                  </div>
                </div>
              </div>
            </section>
            <footer className="modal-card-foot">
              <button className="button" onClick={() => setSelectedBill(null)}>Close</button>
            </footer>
          </div>
        </div>
      )}

      {/* Payment Detail Modal */}
      {selectedPayment && (
        <div className="modal is-active">
          <div className="modal-background" onClick={() => setSelectedPayment(null)}></div>
          <div className="modal-card">
            <header className="modal-card-head">
              <p className="modal-card-title">Payment Details</p>
              <button className="delete" aria-label="close" onClick={() => setSelectedPayment(null)}></button>
            </header>
            <section className="modal-card-body">
              <div className="content">
                <div className="field">
                  <label className="label">Bill ID</label>
                  <div className="control">
                    <input className="input" type="text" value={selectedPayment.billid} readOnly />
                  </div>
                </div>
                <div className="field">
                  <label className="label">Customer</label>
                  <div className="control">
                    <input className="input" type="text" value={selectedPayment.customer?.name || 'Unknown'} readOnly />
                  </div>
                </div>
                <div className="field">
                  <label className="label">Billed Month</label>
                  <div className="control">
                    <input className="input" type="text" value={selectedPayment.billedmonth || 'N/A'} readOnly />
                  </div>
                </div>
                <div className="field">
                  <label className="label">Payment Date</label>
                  <div className="control">
                    <input className="input" type="text" value={selectedPayment.datepaid ? new Date(selectedPayment.datepaid).toLocaleDateString() : 'N/A'} readOnly />
                  </div>
                </div>
                <div className="columns">
                  <div className="column">
                    <div className="field">
                      <label className="label">Basic Amount</label>
                      <div className="control">
                        <input className="input" type="text" value={`${selectedPayment.basicamount?.toFixed(2) || '0.00'}`} readOnly />
                      </div>
                    </div>
                  </div>
                  <div className="column">
                    <div className="field">
                      <label className="label">Surcharge</label>
                      <div className="control">
                        <input className="input" type="text" value={`${selectedPayment.surchargeamount?.toFixed(2) || '0.00'}`} readOnly />
                      </div>
                    </div>
                  </div>
                </div>
                <div className="field">
                  <label className="label">Total Amount</label>
                  <div className="control">
                    <input className="input has-text-weight-bold" type="text" value={`${selectedPayment.totalbillamount?.toFixed(2) || '0.00'}`} readOnly />
                  </div>
                </div>
                <div className="field">
                  <label className="label">Processed By</label>
                  <div className="control">
                    <input className="input" type="text" value={selectedPayment.paidby || 'N/A'} readOnly />
                  </div>
                </div>
                {selectedPayment.advancepaymentamount && selectedPayment.advancepaymentamount > 0 && (
                  <div className="field">
                    <label className="label">Credit Applied</label>
                    <div className="control">
                      <input className="input" type="text" value={`${selectedPayment.advancepaymentamount?.toFixed(2)}`} readOnly />
                    </div>
                  </div>
                )}
              </div>
            </section>
            <footer className="modal-card-foot">
              <button className="button" onClick={() => setSelectedPayment(null)}>Close</button>
            </footer>
          </div>
        </div>
      )}
    </div>
  )
}

export default DashboardHome
