import React, { useState } from 'react'
import { authService } from '../../authService'

const SettingsModal = ({ user, isOpen, onClose }) => {
  const [activeTab, setActiveTab] = useState('security')
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')
  const [success, setSuccess] = useState('')
  
  // Password change state
  const [passwordData, setPasswordData] = useState({
    currentPassword: '',
    newPassword: '',
    confirmPassword: ''
  })

  // Preferences state
  const [preferences, setPreferences] = useState({
    emailNotifications: true,
    browserNotifications: false,
    theme: 'light'
  })

  if (!isOpen) return null

  const handlePasswordChange = (field, value) => {
    setPasswordData(prev => ({ ...prev, [field]: value }))
    setError('')
  }

  const handleChangePassword = async () => {
    setError('')
    setSuccess('')

    // Validation
    if (!passwordData.currentPassword || !passwordData.newPassword || !passwordData.confirmPassword) {
      setError('All password fields are required')
      return
    }

    if (passwordData.newPassword.length < 6) {
      setError('New password must be at least 6 characters long')
      return
    }

    if (passwordData.newPassword !== passwordData.confirmPassword) {
      setError('New passwords do not match')
      return
    }

    setLoading(true)
    try {
      const result = await authService.changePassword(
        user.userid,
        passwordData.currentPassword,
        passwordData.newPassword
      )

      if (result.success) {
        setSuccess('Password changed successfully!')
        setPasswordData({
          currentPassword: '',
          newPassword: '',
          confirmPassword: ''
        })
        setTimeout(() => setSuccess(''), 3000)
      } else {
        setError(result.error || 'Failed to change password')
      }
    } catch (err) {
      setError('An error occurred while changing password')
    }
    setLoading(false)
  }

  const handleSavePreferences = () => {
    setSuccess('Preferences saved successfully!')
    setTimeout(() => setSuccess(''), 2000)
  }

  return (
    <div className="modal is-active">
      <div className="modal-background" onClick={onClose}></div>
      <div className="modal-card" style={{ width: '700px', maxWidth: '90vw' }}>
        <header className="modal-card-head">
          <p className="modal-card-title">
            <i className="fas fa-cog"></i> Settings
          </p>
          <button className="delete" aria-label="close" onClick={onClose}></button>
        </header>
        <section className="modal-card-body" style={{ minHeight: '400px' }}>
          {/* Tabs */}
          <div className="tabs is-boxed">
            <ul>
              <li className={activeTab === 'security' ? 'is-active' : ''}>
                <a onClick={() => setActiveTab('security')}>
                  <span className="icon is-small">
                    <i className="fas fa-lock"></i>
                  </span>
                  <span>Security</span>
                </a>
              </li>
              <li className={activeTab === 'preferences' ? 'is-active' : ''}>
                <a onClick={() => setActiveTab('preferences')}>
                  <span className="icon is-small">
                    <i className="fas fa-sliders-h"></i>
                  </span>
                  <span>Preferences</span>
                </a>
              </li>
              <li className={activeTab === 'about' ? 'is-active' : ''}>
                <a onClick={() => setActiveTab('about')}>
                  <span className="icon is-small">
                    <i className="fas fa-info-circle"></i>
                  </span>
                  <span>About</span>
                </a>
              </li>
            </ul>
          </div>

          {/* Error/Success Messages */}
          {error && (
            <div className="notification is-danger is-light">
              <button className="delete" onClick={() => setError('')}></button>
              {error}
            </div>
          )}
          {success && (
            <div className="notification is-success is-light">
              <button className="delete" onClick={() => setSuccess('')}></button>
              {success}
            </div>
          )}

          {/* Security Tab */}
          {activeTab === 'security' && (
            <div>
              <h3 className="title is-5">
                <i className="fas fa-key"></i> Change Password
              </h3>
              <p className="subtitle is-6" style={{ color: '#7a7a7a', marginBottom: '1.5rem' }}>
                Keep your account secure by using a strong password
              </p>

              <div className="box" style={{ background: '#f9fafb' }}>
                <div className="field">
                  <label className="label">Current Password</label>
                  <div className="control has-icons-left">
                    <input
                      className="input"
                      type="password"
                      placeholder="Enter current password"
                      value={passwordData.currentPassword}
                      onChange={(e) => handlePasswordChange('currentPassword', e.target.value)}
                    />
                    <span className="icon is-small is-left">
                      <i className="fas fa-lock"></i>
                    </span>
                  </div>
                </div>

                <div className="field">
                  <label className="label">New Password</label>
                  <div className="control has-icons-left">
                    <input
                      className="input"
                      type="password"
                      placeholder="Enter new password"
                      value={passwordData.newPassword}
                      onChange={(e) => handlePasswordChange('newPassword', e.target.value)}
                    />
                    <span className="icon is-small is-left">
                      <i className="fas fa-key"></i>
                    </span>
                  </div>
                  <p className="help">Password must be at least 6 characters long</p>
                </div>

                <div className="field">
                  <label className="label">Confirm New Password</label>
                  <div className="control has-icons-left">
                    <input
                      className="input"
                      type="password"
                      placeholder="Confirm new password"
                      value={passwordData.confirmPassword}
                      onChange={(e) => handlePasswordChange('confirmPassword', e.target.value)}
                    />
                    <span className="icon is-small is-left">
                      <i className="fas fa-check-circle"></i>
                    </span>
                  </div>
                </div>

                <div className="field" style={{ marginTop: '1.5rem' }}>
                  <div className="control">
                    <button
                      className={`button is-primary ${loading ? 'is-loading' : ''}`}
                      onClick={handleChangePassword}
                      disabled={loading}
                    >
                      <span className="icon">
                        <i className="fas fa-save"></i>
                      </span>
                      <span>Update Password</span>
                    </button>
                  </div>
                </div>
              </div>

              {/* Security Tips */}
              <div className="notification is-info is-light" style={{ marginTop: '1.5rem' }}>
                <strong>Security Tips:</strong>
                <ul style={{ marginTop: '0.5rem', paddingLeft: '1.5rem' }}>
                  <li>Use a unique password for this account</li>
                  <li>Include uppercase, lowercase, numbers, and symbols</li>
                  <li>Avoid using personal information in your password</li>
                  <li>Change your password regularly</li>
                </ul>
              </div>
            </div>
          )}

          {/* Preferences Tab */}
          {activeTab === 'preferences' && (
            <div>
              <h3 className="title is-5">
                <i className="fas fa-sliders-h"></i> Application Preferences
              </h3>
              <p className="subtitle is-6" style={{ color: '#7a7a7a', marginBottom: '1.5rem' }}>
                Customize your experience
              </p>

              <div className="box" style={{ background: '#f9fafb' }}>
                {/* Notifications */}
                <div className="field" style={{ marginBottom: '1.5rem' }}>
                  <label className="label">
                    <i className="fas fa-bell"></i> Notifications
                  </label>
                  <div className="field">
                    <input
                      id="emailNotif"
                      type="checkbox"
                      className="switch is-rounded is-success"
                      checked={preferences.emailNotifications}
                      onChange={(e) => setPreferences(prev => ({ ...prev, emailNotifications: e.target.checked }))}
                    />
                    <label htmlFor="emailNotif">Enable email notifications</label>
                  </div>
                  <div className="field">
                    <input
                      id="browserNotif"
                      type="checkbox"
                      className="switch is-rounded is-info"
                      checked={preferences.browserNotifications}
                      onChange={(e) => setPreferences(prev => ({ ...prev, browserNotifications: e.target.checked }))}
                    />
                    <label htmlFor="browserNotif">Enable browser notifications</label>
                  </div>
                </div>

                {/* Theme */}
                <div className="field">
                  <label className="label">
                    <i className="fas fa-palette"></i> Theme (Coming Soon)
                  </label>
                  <div className="control">
                    <div className="select is-fullwidth" disabled>
                      <select
                        value={preferences.theme}
                        onChange={(e) => setPreferences(prev => ({ ...prev, theme: e.target.value }))}
                        disabled
                      >
                        <option value="light">Light Mode</option>
                        <option value="dark">Dark Mode</option>
                        <option value="auto">Auto (System)</option>
                      </select>
                    </div>
                  </div>
                  <p className="help">Theme customization will be available in a future update</p>
                </div>

                <div className="field" style={{ marginTop: '1.5rem' }}>
                  <div className="control">
                    <button className="button is-primary" onClick={handleSavePreferences}>
                      <span className="icon">
                        <i className="fas fa-save"></i>
                      </span>
                      <span>Save Preferences</span>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          )}

          {/* About Tab */}
          {activeTab === 'about' && (
            <div>
              <h3 className="title is-5">
                <i className="fas fa-info-circle"></i> About This System
              </h3>
              
              <div className="box" style={{ background: '#f9fafb', textAlign: 'center', padding: '2rem' }}>
                <div style={{
                  width: '80px',
                  height: '80px',
                  borderRadius: '16px',
                  background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
                  display: 'inline-flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  fontSize: '2rem',
                  color: 'white',
                  marginBottom: '1rem'
                }}>
                  <i className="fas fa-building"></i>
                </div>
                <h3 className="title is-4">Water Billing System</h3>
                <p className="subtitle is-6" style={{ color: '#7a7a7a' }}>
                  LGU Concepcion, Romblon
                </p>
              </div>

              <div className="content" style={{ marginTop: '1.5rem' }}>
                <table className="table is-fullwidth is-striped">
                  <tbody>
                    <tr>
                      <td><strong>Version</strong></td>
                      <td>2.0.0</td>
                    </tr>
                    <tr>
                      <td><strong>Build Date</strong></td>
                      <td>October 2025</td>
                    </tr>
                    <tr>
                      <td><strong>Technology</strong></td>
                      <td>React.js + Supabase</td>
                    </tr>
                    <tr>
                      <td><strong>Database</strong></td>
                      <td>PostgreSQL (Supabase)</td>
                    </tr>
                    <tr>
                      <td><strong>UI Framework</strong></td>
                      <td>Bulma CSS</td>
                    </tr>
                  </tbody>
                </table>
              </div>

              <div className="notification is-link is-light">
                <p><strong>Need Help?</strong></p>
                <p style={{ marginTop: '0.5rem' }}>
                  Contact your system administrator or IT support team for assistance with this application.
                </p>
              </div>
            </div>
          )}
        </section>
        <footer className="modal-card-foot" style={{ justifyContent: 'flex-end' }}>
          <button className="button" onClick={onClose}>
            Close
          </button>
        </footer>
      </div>
    </div>
  )
}

export default SettingsModal
