import React, { useState, useEffect } from 'react'
import { rhuService } from '../../../../services/rhuService'

const RuralHealthHome = ({ user }) => {
  const [pendingOrders, setPendingOrders] = useState([])
  const [paidOrders, setPaidOrders] = useState([])
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')
  const [success, setSuccess] = useState('')
  const [viewingOrder, setViewingOrder] = useState(null)
  const [processingPayment, setProcessingPayment] = useState(null)
  const [orNumber, setOrNumber] = useState('')

  useEffect(() => {
    loadOrders()
  }, [])

  const loadOrders = async () => {
    setLoading(true)
    const result = await rhuService.getAllOrders()
    if (result.success) {
      // Filter pending and paid orders
      const pending = result.data.filter(order => order.status === 'pending')
      const paid = result.data.filter(order => order.status === 'paid')
        .slice(0, 10) // Show only 10 most recent paid orders
      setPendingOrders(pending)
      setPaidOrders(paid)
    } else {
      setError(result.error)
      setTimeout(() => setError(''), 3000)
    }
    setLoading(false)
  }

  const loadPendingOrders = loadOrders // Keep compatibility

  const calculateAge = (birthdate) => {
    if (!birthdate) return ''
    const today = new Date()
    const birth = new Date(birthdate)
    let age = today.getFullYear() - birth.getFullYear()
    const monthDiff = today.getMonth() - birth.getMonth()
    if (monthDiff < 0 || (monthDiff === 0 && today.getDate() < birth.getDate())) {
      age--
    }
    return age
  }

  const handleViewOrder = async (order) => {
    setLoading(true)
    const result = await rhuService.getOrderWithItems(order.id)
    if (result.success) {
      setViewingOrder(result.data)
    } else {
      setError(result.error)
      setTimeout(() => setError(''), 3000)
    }
    setLoading(false)
  }

  const openPaymentModal = (order) => {
    setProcessingPayment(order)
    setOrNumber('')
  }

  const handleProcessPayment = async () => {
    if (!orNumber.trim()) {
      setError('Please enter OR Number')
      setTimeout(() => setError(''), 3000)
      return
    }

    setLoading(true)
    // Get the current user's full name
    const processedBy = `${user.firstname} ${user.lastname}`
    const result = await rhuService.updateOrderStatus(processingPayment.id, 'paid', orNumber, processedBy)
    
    if (result.success) {
      setSuccess(`Payment processed successfully! OR #${orNumber}`)
      setProcessingPayment(null)
      setOrNumber('')
      loadOrders()
      setTimeout(() => setSuccess(''), 3000)
    } else {
      setError(result.error)
      setTimeout(() => setError(''), 3000)
    }
    setLoading(false)
  }

  return (
    <>
      {/* CSS Animations */}
      <style>{`
        @keyframes fadeInUp {
          from { opacity: 0; transform: translateY(20px); }
          to { opacity: 1; transform: translateY(0); }
        }
        @keyframes fadeIn {
          from { opacity: 0; }
          to { opacity: 1; }
        }
        @keyframes slideInDown {
          from { opacity: 0; transform: translateY(-10px); }
          to { opacity: 1; transform: translateY(0); }
        }
        @keyframes pulse {
          0%, 100% { transform: scale(1); opacity: 1; }
          50% { transform: scale(1.05); opacity: 0.8; }
        }
      `}</style>

    <div className="dashboard-home">
      {/* Welcome Section */}
      <div className="welcome-section">
        <div>
          <h1 className="welcome-title">
            Welcome to Rural Health Unit, {user.firstname}! 
          </h1>
          <p className="welcome-subtitle">
            Rural Health Unit Management System
          </p>
        </div>
      </div>

      <div className="section" style={{ padding: '1.5rem 1rem' }}>
        <div className="container" style={{ maxWidth: '1400px' }}>
          {/* Header */}
          <div style={{ 
            background: 'linear-gradient(135deg, #10b981 0%, #059669 100%)', 
            borderRadius: '16px',
            padding: '2rem',
            marginBottom: '2rem',
            boxShadow: '0 10px 30px rgba(16, 185, 129, 0.2)'
          }}>
            <div style={{ display: 'flex', alignItems: 'center', flexWrap: 'wrap' }}>
              <div style={{ flex: 1 }}>
                <h2 className="title is-4" style={{ 
                  color: 'white', 
                  marginBottom: '0.5rem',
                  display: 'flex',
                  alignItems: 'center'
                }}>
                  <i className="fas fa-clock" style={{ marginRight: '0.75rem' }}></i>
                  Pending Orders of Payment
                </h2>
                <p style={{ color: 'rgba(255, 255, 255, 0.9)', marginBottom: 0 }}>
                  {pendingOrders.length} order{pendingOrders.length !== 1 ? 's' : ''} awaiting payment
                </p>
              </div>
              <div>
                <button 
                  className="button is-light"
                  onClick={loadOrders}
                  style={{ borderRadius: '10px' }}
                >
                  <span className="icon">
                    <i className="fas fa-sync-alt"></i>
                  </span>
                  <span>Refresh</span>
                </button>
              </div>
            </div>
          </div>

          {/* Success Message */}
          {success && (
            <div className="notification is-success is-light" style={{
              borderRadius: '12px',
              border: '2px solid #48c774',
              animation: 'slideInDown 0.3s ease',
              marginBottom: '1.5rem'
            }}>
              <button className="delete" onClick={() => setSuccess('')}></button>
              <i className="fas fa-check-circle" style={{ marginRight: '0.5rem' }}></i>
              {success}
            </div>
          )}

          {/* Error Message */}
          {error && (
            <div className="notification is-danger is-light" style={{
              borderRadius: '12px',
              border: '2px solid #f14668',
              animation: 'slideInDown 0.3s ease',
              marginBottom: '1.5rem'
            }}>
              <button className="delete" onClick={() => setError('')}></button>
              <i className="fas fa-exclamation-circle" style={{ marginRight: '0.5rem' }}></i>
              {error}
            </div>
          )}

          {/* Loading */}
          {loading && (
            <div className="notification is-info is-light" style={{
              borderRadius: '12px',
              textAlign: 'center',
              marginBottom: '1.5rem'
            }}>
              <span className="icon" style={{ fontSize: '1.5rem' }}>
                <i className="fas fa-spinner fa-spin"></i>
              </span>
              <span style={{ marginLeft: '0.5rem', fontSize: '1.1rem' }}>Loading...</span>
            </div>
          )}

          {/* Pending Orders List */}
          {pendingOrders.length === 0 && !loading ? (
            <div className="box" style={{ 
              textAlign: 'center', 
              padding: '4rem 2rem',
              borderRadius: '16px',
              border: '2px dashed #e5e7eb',
              background: 'linear-gradient(135deg, #f0fdf4 0%, #dcfce7 100%)'
            }}>
              <div style={{ 
                fontSize: '4rem', 
                color: '#10b981', 
                marginBottom: '1.5rem',
                animation: 'pulse 2s infinite'
              }}>
                <i className="fas fa-check-circle"></i>
              </div>
              <h3 className="title is-4" style={{ color: '#363636' }}>All Caught Up!</h3>
              <p className="subtitle is-6" style={{ color: '#6b7280' }}>
                No pending orders of payment at the moment
              </p>
            </div>
          ) : (
            <div className="box" style={{
              borderRadius: '16px',
              padding: '1.5rem',
              boxShadow: '0 4px 12px rgba(0, 0, 0, 0.08)'
            }}>
              <div style={{ overflowX: 'auto' }}>
                <table className="table is-fullwidth is-hoverable" style={{ 
                  background: 'white',
                  borderRadius: '10px'
                }}>
                  <thead>
                    <tr style={{ background: '#f8f9fa' }}>
                      <th style={{ borderRadius: '10px 0 0 0' }}>Order #</th>
                      <th>Patient Name</th>
                      <th>Age</th>
                      <th>Address</th>
                      <th>Total Amount</th>
                      <th>Date</th>
                      <th style={{ borderRadius: '0 10px 0 0', width: '180px' }}>Actions</th>
                    </tr>
                  </thead>
                  <tbody>
                    {pendingOrders.map((order, index) => (
                      <tr 
                        key={order.id}
                        style={{
                          animation: `fadeIn 0.3s ease ${index * 0.05}s both`,
                          transition: 'all 0.3s ease'
                        }}
                        onMouseEnter={(e) => e.currentTarget.style.background = '#f0fdf4'}
                        onMouseLeave={(e) => e.currentTarget.style.background = ''}
                      >
                        <td className="has-text-weight-semibold" style={{ color: '#10b981' }}>
                          {order.order_number}
                        </td>
                        <td className="has-text-weight-semibold">{order.patient?.name || 'N/A'}</td>
                        <td>{calculateAge(order.patient?.birthdate)} years</td>
                        <td style={{ maxWidth: '200px', whiteSpace: 'nowrap', overflow: 'hidden', textOverflow: 'ellipsis' }}>
                          {order.patient?.address || 'N/A'}
                        </td>
                        <td>
                          <span className="tag is-warning is-medium" style={{ 
                            borderRadius: '8px',
                            fontWeight: '700',
                            fontSize: '1rem',
                            padding: '0.5rem 1rem'
                          }}>
                            {parseFloat(order.total_amount).toFixed(2)}
                          </span>
                        </td>
                        <td>{new Date(order.created_at).toLocaleDateString()}</td>
                        <td>
                          <div className="buttons are-small">
                            <button 
                              className="button is-info is-light is-small"
                              onClick={() => handleViewOrder(order)}
                              style={{ borderRadius: '8px' }}
                            >
                              <span className="icon">
                                <i className="fas fa-eye"></i>
                              </span>
                              <span>View</span>
                            </button>
                            <button 
                              className="button is-success is-light is-small"
                              onClick={() => openPaymentModal(order)}
                              style={{ borderRadius: '8px' }}
                            >
                              <span className="icon">
                                <i className="fas fa-dollar-sign"></i>
                              </span>
                              <span>Pay</span>
                            </button>
                          </div>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          )}

          {/* Recent Paid Orders Section */}
          <div style={{ 
            background: 'linear-gradient(135deg, #3b82f6 0%, #2563eb 100%)', 
            borderRadius: '16px',
            padding: '2rem',
            marginTop: '2rem',
            marginBottom: '2rem',
            boxShadow: '0 10px 30px rgba(59, 130, 246, 0.2)'
          }}>
            <div style={{ display: 'flex', alignItems: 'center', flexWrap: 'wrap' }}>
              <div style={{ flex: 1 }}>
                <h2 className="title is-4" style={{ 
                  color: 'white', 
                  marginBottom: '0.5rem',
                  display: 'flex',
                  alignItems: 'center'
                }}>
                  <i className="fas fa-check-circle" style={{ marginRight: '0.75rem' }}></i>
                  Recent Paid Orders
                </h2>
                <p style={{ color: 'rgba(255, 255, 255, 0.9)', marginBottom: 0 }}>
                  Last {paidOrders.length} completed payment{paidOrders.length !== 1 ? 's' : ''}
                </p>
              </div>
            </div>
          </div>

          {/* Paid Orders List */}
          {paidOrders.length === 0 && !loading ? (
            <div className="box" style={{ 
              textAlign: 'center', 
              padding: '3rem 2rem',
              borderRadius: '16px',
              border: '2px dashed #e5e7eb',
              background: 'linear-gradient(135deg, #eff6ff 0%, #dbeafe 100%)'
            }}>
              <div style={{ 
                fontSize: '3rem', 
                color: '#3b82f6', 
                marginBottom: '1rem'
              }}>
                <i className="fas fa-file-invoice-dollar"></i>
              </div>
              <h3 className="title is-5" style={{ color: '#363636' }}>No Paid Orders Yet</h3>
              <p className="subtitle is-6" style={{ color: '#6b7280' }}>
                Completed payments will appear here
              </p>
            </div>
          ) : (
            <div className="box" style={{
              borderRadius: '16px',
              padding: '1.5rem',
              boxShadow: '0 4px 12px rgba(0, 0, 0, 0.08)'
            }}>
              <div style={{ overflowX: 'auto' }}>
                <table className="table is-fullwidth is-hoverable" style={{ 
                  background: 'white',
                  borderRadius: '10px'
                }}>
                  <thead>
                    <tr style={{ background: '#f8f9fa' }}>
                      <th style={{ borderRadius: '10px 0 0 0' }}>Order #</th>
                      <th>OR Number</th>
                      <th>Patient Name</th>
                      <th>Age</th>
                      <th>Total Amount</th>
                      <th>Payment Date</th>
                      <th>Processed By</th>
                      <th style={{ borderRadius: '0 10px 0 0', width: '120px' }}>Actions</th>
                    </tr>
                  </thead>
                  <tbody>
                    {paidOrders.map((order, index) => (
                      <tr 
                        key={order.id}
                        style={{
                          animation: `fadeIn 0.3s ease ${index * 0.05}s both`,
                          transition: 'all 0.3s ease'
                        }}
                        onMouseEnter={(e) => e.currentTarget.style.background = '#eff6ff'}
                        onMouseLeave={(e) => e.currentTarget.style.background = ''}
                      >
                        <td className="has-text-weight-semibold" style={{ color: '#3b82f6' }}>
                          {order.order_number}
                        </td>
                        <td>
                          <span className="tag is-success" style={{ 
                            borderRadius: '8px',
                            fontWeight: '600'
                          }}>
                            {order.or_number || 'N/A'}
                          </span>
                        </td>
                        <td className="has-text-weight-semibold">{order.patient?.name || 'N/A'}</td>
                        <td>{calculateAge(order.patient?.birthdate)} years</td>
                        <td>
                          <span className="tag is-warning is-medium" style={{ 
                            borderRadius: '8px',
                            fontWeight: '700',
                            fontSize: '1rem',
                            padding: '0.5rem 1rem'
                          }}>
                            {parseFloat(order.total_amount).toFixed(2)}
                          </span>
                        </td>
                        <td>
                          {order.paid_at ? new Date(order.paid_at).toLocaleDateString() + ' ' + 
                           new Date(order.paid_at).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' }) 
                           : 'N/A'}
                        </td>
                        <td>
                          <span style={{ fontSize: '0.9rem' }}>
                            {order.processed_by || 'N/A'}
                          </span>
                        </td>
                        <td>
                          <button 
                            className="button is-info is-light is-small"
                            onClick={() => handleViewOrder(order)}
                            style={{ borderRadius: '8px' }}
                          >
                            <span className="icon">
                              <i className="fas fa-eye"></i>
                            </span>
                            <span>View</span>
                          </button>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          )}

          {/* View Order Modal */}
          {viewingOrder && (
            <div className="modal is-active">
              <div className="modal-background" onClick={() => setViewingOrder(null)} style={{ background: 'rgba(0, 0, 0, 0.6)' }}></div>
              <div className="modal-card" style={{ 
                borderRadius: '16px', 
                overflow: 'hidden',
                animation: 'fadeInUp 0.3s ease',
                maxWidth: '700px'
              }}>
                <header className="modal-card-head" style={{ 
                  background: 'linear-gradient(135deg, #3b82f6 0%, #2563eb 100%)',
                  borderBottom: 'none'
                }}>
                  <p className="modal-card-title" style={{ color: 'white', fontWeight: '600' }}>
                    <i className="fas fa-file-invoice" style={{ marginRight: '0.5rem' }}></i>
                    Order Details - {viewingOrder.order_number}
                  </p>
                  <button className="delete" onClick={() => setViewingOrder(null)} style={{ background: 'rgba(255, 255, 255, 0.3)' }}></button>
                </header>
                <section className="modal-card-body" style={{ padding: '2rem' }}>
                  {/* Patient Info */}
                  <div style={{ marginBottom: '2rem', padding: '1.5rem', background: '#eff6ff', borderRadius: '12px' }}>
                    <h3 className="title is-6" style={{ marginBottom: '1rem', color: '#1e40af' }}>
                      <i className="fas fa-user" style={{ marginRight: '0.5rem' }}></i>
                      Patient Information
                    </h3>
                    <p><strong>Name:</strong> {viewingOrder.patient?.name}</p>
                    <p><strong>Age:</strong> {calculateAge(viewingOrder.patient?.birthdate)} years</p>
                    <p><strong>Address:</strong> {viewingOrder.patient?.address}</p>
                  </div>

                  {/* Order Items */}
                  <div style={{ marginBottom: '1.5rem' }}>
                    <h3 className="title is-6" style={{ marginBottom: '1rem', color: '#1e40af' }}>
                      <i className="fas fa-list" style={{ marginRight: '0.5rem' }}></i>
                      Services
                    </h3>
                    <table className="table is-fullwidth">
                      <thead>
                        <tr style={{ background: '#f8f9fa' }}>
                          <th>Service</th>
                          <th style={{ width: '80px' }}>Qty</th>
                          <th style={{ width: '120px' }}>Price</th>
                          <th style={{ width: '120px' }}>Subtotal</th>
                        </tr>
                      </thead>
                      <tbody>
                        {viewingOrder.items?.map(item => (
                          <tr key={item.id}>
                            <td>{item.service_name}</td>
                            <td>{item.quantity}</td>
                            <td>{parseFloat(item.unit_price).toFixed(2)}</td>
                            <td style={{ fontWeight: '700' }}>{parseFloat(item.subtotal).toFixed(2)}</td>
                          </tr>
                        ))}
                      </tbody>
                      <tfoot>
                        <tr style={{ background: '#eff6ff' }}>
                          <td colSpan="3" style={{ textAlign: 'right', fontWeight: '700', fontSize: '1.1rem' }}>
                            TOTAL:
                          </td>
                          <td style={{ fontWeight: '700', fontSize: '1.2rem', color: '#3b82f6' }}>
                            {parseFloat(viewingOrder.total_amount).toFixed(2)}
                          </td>
                        </tr>
                      </tfoot>
                    </table>
                  </div>

                  {/* Payment Information for Paid Orders */}
                  {viewingOrder.status === 'paid' && (
                    <div style={{ marginBottom: '1.5rem', padding: '1.5rem', background: '#f0fdf4', borderRadius: '12px', border: '1px solid #bbf7d0' }}>
                      <h3 className="title is-6" style={{ marginBottom: '1rem', color: '#166534' }}>
                        <i className="fas fa-check-circle" style={{ marginRight: '0.5rem' }}></i>
                        Payment Information
                      </h3>
                      <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '1rem' }}>
                        <div>
                          <p style={{ fontSize: '0.9rem', color: '#6b7280', marginBottom: '0.25rem' }}>OR Number:</p>
                          <p style={{ fontSize: '1.1rem', fontWeight: '700', color: '#166534' }}>
                            {viewingOrder.or_number || 'N/A'}
                          </p>
                        </div>
                        <div>
                          <p style={{ fontSize: '0.9rem', color: '#6b7280', marginBottom: '0.25rem' }}>Payment Date:</p>
                          <p style={{ fontSize: '1.1rem', fontWeight: '700', color: '#166534' }}>
                            {viewingOrder.paid_at ? new Date(viewingOrder.paid_at).toLocaleString() : 'N/A'}
                          </p>
                        </div>
                        <div style={{ gridColumn: '1 / -1' }}>
                          <p style={{ fontSize: '0.9rem', color: '#6b7280', marginBottom: '0.25rem' }}>Processed By:</p>
                          <p style={{ fontSize: '1.1rem', fontWeight: '700', color: '#166534' }}>
                            <i className="fas fa-user" style={{ marginRight: '0.5rem' }}></i>
                            {viewingOrder.processed_by || 'N/A'}
                          </p>
                        </div>
                      </div>
                    </div>
                  )}

                  {/* Status */}
                  <div style={{ textAlign: 'center' }}>
                    <span className={`tag is-large ${viewingOrder.status === 'paid' ? 'is-success' : 'is-warning'}`} style={{ borderRadius: '10px' }}>
                      Status: {viewingOrder.status === 'paid' ? 'PAID' : 'PENDING PAYMENT'}
                    </span>
                  </div>
                </section>
                <footer className="modal-card-foot" style={{ 
                  borderTop: '1px solid #e5e7eb',
                  background: '#f9fafb',
                  justifyContent: viewingOrder.status === 'paid' ? 'center' : 'space-between'
                }}>
                  {viewingOrder.status === 'paid' ? (
                    <button className="button is-info" onClick={() => setViewingOrder(null)} style={{ borderRadius: '10px' }}>Close</button>
                  ) : (
                    <>
                      <button className="button" onClick={() => setViewingOrder(null)} style={{ borderRadius: '10px' }}>Close</button>
                      <button 
                        className="button is-success" 
                        onClick={() => {
                          openPaymentModal(viewingOrder)
                          setViewingOrder(null)
                        }}
                        style={{ borderRadius: '10px' }}
                      >
                        <span className="icon">
                          <i className="fas fa-dollar-sign"></i>
                        </span>
                        <span>Process Payment</span>
                      </button>
                    </>
                  )}
                </footer>
              </div>
            </div>
          )}

          {/* Process Payment Modal */}
          {processingPayment && (
            <div className="modal is-active">
              <div className="modal-background" onClick={() => setProcessingPayment(null)} style={{ background: 'rgba(0, 0, 0, 0.6)' }}></div>
              <div className="modal-card" style={{ 
                borderRadius: '16px', 
                overflow: 'hidden',
                animation: 'fadeInUp 0.3s ease',
                maxWidth: '500px'
              }}>
                <header className="modal-card-head" style={{ 
                  background: 'linear-gradient(135deg, #10b981 0%, #059669 100%)',
                  borderBottom: 'none'
                }}>
                  <p className="modal-card-title" style={{ color: 'white', fontWeight: '600' }}>
                    <i className="fas fa-dollar-sign" style={{ marginRight: '0.5rem' }}></i>
                    Process Payment
                  </p>
                  <button className="delete" onClick={() => setProcessingPayment(null)} style={{ background: 'rgba(255, 255, 255, 0.3)' }}></button>
                </header>
                <section className="modal-card-body" style={{ padding: '2rem' }}>
                  {/* Payment Summary */}
                  <div style={{ marginBottom: '2rem', padding: '1.5rem', background: '#f0fdf4', borderRadius: '12px', border: '1px solid #bbf7d0' }}>
                    <h3 className="title is-6" style={{ marginBottom: '1rem', color: '#166534' }}>
                      <i className="fas fa-info-circle" style={{ marginRight: '0.5rem' }}></i>
                      Payment Summary
                    </h3>
                    <p><strong>Order Number:</strong> {processingPayment.order_number}</p>
                    <p><strong>Patient:</strong> {processingPayment.patient?.name}</p>
                    <p style={{ fontSize: '1.3rem', marginTop: '1rem' }}>
                      <strong>Amount to Pay:</strong> 
                      <span style={{ color: '#10b981', fontWeight: '700', marginLeft: '0.5rem' }}>
                        {parseFloat(processingPayment.total_amount).toFixed(2)}
                      </span>
                    </p>
                  </div>

                  {/* OR Number Input */}
                  <div className="field">
                    <label className="label" style={{ fontWeight: '600', color: '#2c3e50' }}>
                      <i className="fas fa-receipt" style={{ marginRight: '0.5rem', color: '#10b981' }}></i>
                      Official Receipt (OR) Number *
                    </label>
                    <div className="control">
                      <input 
                        className="input is-large"
                        type="text"
                        placeholder="Enter OR Number"
                        value={orNumber}
                        onChange={(e) => setOrNumber(e.target.value)}
                        autoFocus
                        style={{ 
                          borderRadius: '10px',
                          border: '2px solid #e5e7eb',
                          fontSize: '1.2rem',
                          textAlign: 'center',
                          fontWeight: '600',
                          transition: 'all 0.3s ease'
                        }}
                        onFocus={(e) => e.target.style.borderColor = '#10b981'}
                        onBlur={(e) => e.target.style.borderColor = '#e5e7eb'}
                      />
                    </div>
                    <p className="help" style={{ textAlign: 'center', marginTop: '0.5rem' }}>
                      Please enter the official receipt number for this payment
                    </p>
                  </div>
                </section>
                <footer className="modal-card-foot" style={{ 
                  borderTop: '1px solid #e5e7eb',
                  background: '#f9fafb',
                  justifyContent: 'flex-end',
                  gap: '0.75rem'
                }}>
                  <button className="button" onClick={() => setProcessingPayment(null)} style={{ borderRadius: '10px' }}>Cancel</button>
                  <button 
                    className="button is-success is-large" 
                    onClick={handleProcessPayment}
                    disabled={loading || !orNumber.trim()}
                    style={{ 
                      borderRadius: '10px',
                      fontWeight: '600'
                    }}
                  >
                    <span className="icon">
                      <i className="fas fa-check-circle"></i>
                    </span>
                    <span>Confirm Payment</span>
                  </button>
                </footer>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
    </>
  )
}

export default RuralHealthHome
